    //let sampleDataForStopMedia ="{\"type\": \"TOUR_TOOL_INSPECTOR\",\"value\":{\"action\":\"Stop\"}}";
    // const data = JSON.parse(sampleDataForStopMedia);
    //---------------------------------------------------------------------------------------
    //------------sample data for cam Media tour inspector-----------------------------------
    // let sampleDataMediaInspector = "{\"value\":{\"objectName\":\"Obj_TourTool_16#0\",\"position\":{\"x\":11.36604,\"y\":-69.5070343,\"z\":9.867604},\"rotation\":{\"x\":-2.50447852E-06,\"y\":17.5840149,\"z\":0.0},\"scale\":{\"x\":1.0,\"y\":1.0,\"z\":1.0},\"locked\":false,\"hidden\":false},\"type\":\"MEDIA_TOOL_INSPECTOR\"}";
