    // const data = JSON.parse(sampleDataUserTagLegend);
    //----------------------------------------------------------------------------------------------
    //let showRoomPresenterData="{\"type\": \"STATE_SWITCH\",\"value\":{\"role\":\"FreeTrialStage1\",\"stateMode\":\"\",\"microstate\":[{\"macrostate\":\"Dashboard\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Refresh\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"ProjectLoader\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"ObjectLoader\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Tools\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"MediaObject\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Measure\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"POV\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Text\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Grid\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"VolumetricLights\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"CameraPath\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Delete\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Scale_ResetScale\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Undo_Redo\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Copy_Paste\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Group_Ungroup\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"ViewToggle_HomeView\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"UserTag\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"LayoutSettings\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"GridUnits\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Lighting\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"EnvironmentalToggle\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Hide_Show\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Show_Hide_Physical_Object\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"EnableTransform\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Inspector\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"StateSwitch\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"Tooltips\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"ExportPDF\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"SavePDF\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"ShowCaptions\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false},{\"macrostate\":\"ShowLegend\",\"Available\":false,\"Hover\":false,\"Clicked\":false,\"Disabled\":true,\"Hidden\":false}]}}";
