  // };
  handleJoyrideCallback = data => {
    //const { action, index, status, type } = data;
    const { action, status, type } = data;
    if ([EVENTS.STEP_AFTER, EVENTS.TARGET_NOT_FOUND].includes(type)) {
      // Update state to advance the tour
      // this.setState({ stepIndex: index + (action === ACTIONS.PREV ? -1 : 1) });
      if (action === ACTIONS.CLOSE) {
        this.setState({ run: false, objectLoaderDragEnd: false });
        if (window.vuplex) {
          window.vuplex.postMessage({
            type: MessageType.PROJECT_TOUR, value: '{"action":"Close"}'
          });
        }
      }
    }
    else if ([STATUS.FINISHED, STATUS.SKIPPED].includes(status)) {
      // Need to set our running state to false, so we can restart if we click start again.
      this.setState({ run: false, objectLoaderDragEnd: false });
      if (window.vuplex) {
        window.vuplex.postMessage({
          type: MessageType.PROJECT_TOUR, value: '{"action":"Close"}'
        });
      }
    }
    //  console.log(this.state.run);
    //     console.groupCollapsed(type);
    //     console.log(data); //eslint-disable-line no-console
    //     console.groupEnd();
  };

  toolTipClickHandler = (event) => {
    event.preventDefault();
    this.setState({ run: true });

    if (window.vuplex) {
      window.vuplex.postMessage({
        type: MessageType.PROJECT_TOUR, value: '{"action":"Open"}'
      });
    }
    if (window.vuplex) {
      window.vuplex.postMessage({ type: MessageType.INFORMATION, value: "INFORMATION" });
    }
  }

