import React,{ Component } from "react";
import Draggable from 'react-draggable';



export default class LegendPanel extends Component<{LegendPanelCss,legendData}>{


state = {
    legendData:{},
}



 componentDidMount()
 {

 }  
 static getDerivedStateFromProps(props, state) {
     if(props.legendData.length>0)
     {
     let legendJsonValue= props.legendData;
    // console.log(legendJsonValue);
     return{
         legendData:legendJsonValue,
     }
    }
    return null;
 }

 legendValues = ({ legendValue }) => {

   return(
    legendValue.map((legendData, key) => (

        <div key={key} className="legend-item">
        <div className="tab left">
            <svg xmlns="http://www.w3.org/2000/svg" width="52" height="26" 
            viewBox="0 0 52 26"><path d="M39,26H0V0H39A13,13,0,0,1,52,13h0A13,13,0,0,1,39,26Z"
             fill={"#"+legendData.objectColor}/></svg>
        </div>
    <div className="category left"><h3>{legendData.objectName}</h3></div>
    </div>
        ))

   );
}
renderLegendData()
{
    return(
  <this.legendValues legendValue={this.state.legendData} />
    );
}

render()
{
    return(
        
        // <!--========== PANELS ==========-->
        // <!-- Draggable Panel: Legend -->
        <Draggable axis="x"  grid={[26, 26]}>
        <div id="legendPanel" className={"legend "+this.props.LegendPanelCss}>
            <div className="legend-container">
                <div id="legendPanelheader" className="legend-handle grab">
                    {/* <!-- Grab handle --> */}
                    <svg xmlns="http://www.w3.org/2000/svg" width="25.3" height="4.9" 
                    viewBox="0 0 25.3 4.9"><line x1="0.65" x2="0.65" y2="4.9" fill="none" 
                    stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/>
                    <line x1="3.65" x2="3.65" y2="4.9" fill="none" stroke="#cdcdcd" 
                    stroke-miterlimit="10" stroke-width="1.3"/><line x1="6.65" x2="6.65" 
                    y2="4.9" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/>
                    <line x1="9.65" x2="9.65" y2="4.9" fill="none" stroke="#cdcdcd" stroke-miterlimit="10"
                     stroke-width="1.3"/><line x1="12.65" x2="12.65" y2="4.9" fill="none" stroke="#cdcdcd"
                      stroke-miterlimit="10" stroke-width="1.3"/><line x1="15.65" x2="15.65" y2="4.9"
                       fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/>
                       <line x1="18.65" x2="18.65" y2="4.9" fill="none" stroke="#cdcdcd" 
                       stroke-miterlimit="10" stroke-width="1.3"/><line x1="21.65" x2="21.65" 
                       y2="4.9" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/>
                       <line x1="24.65" x2="24.65" y2="4.9" fill="none" stroke="#cdcdcd"
                        stroke-miterlimit="10" stroke-width="1.3"/></svg>
                </div>
                {/* <!-- Title of Legend --> */}
                <div className="legend-title">
                    <h1>Legend</h1>
                </div>
                {/* <!-- Legend Items --> */}
                <div className="legend-item-container"> 
                    {/* <!-- Legend Item 1 (Placeholder - For Mockup Only) --> */}
                    {
                        this.props.legendData.length>0 ?
                        this.renderLegendData()
                        :null
                    
                    }
                </div>
            </div>
        </div>
        </Draggable>
        // <!-- /legend -->
        


    );
}

}