  showHideObjects = () => {
    // Return a list of all elements with class "object"

    var objRefList = document.getElementById("objectLoader") as HTMLElement;

    var objList = objRefList.querySelectorAll(".object");

    var objBtnRef;
    // Convert list of objects to an array
    var objListArr = Array.prototype.slice.call(objList);

    // Return a list of all elements with class "pin"
    // var pinsRef = document.getElementById("objectLoader") as HTMLElement;
    var pins = objRefList.querySelectorAll(".pin");
    // Convert list of objects to an array
    var pinsArr = Array.prototype.slice.call(pins);

    // Return a list of all elements with class "pin"
    // var detailsRef = document.getElementById("objectLoader") as HTMLElement;
    var details = objRefList.querySelectorAll(".object-details");
    // Convert list of objects to an array
    var detailsArr = Array.prototype.slice.call(details);
    var objHover;
    for (var indexOfObj = 0; indexOfObj < objListArr.length; indexOfObj++) {
      objListArr[indexOfObj].addEventListener('mouseover', function (indexOfObj) {
        for (objHover = 0; objHover < objListArr.length; objHover++) {
          if (indexOfObj == objHover) {
            // Show pin when hovering on object
            pinsArr[objHover].classList.add("show");
            pinsArr[objHover].classList.remove("hide");
            // Show details when hovering on object
            detailsArr[objHover].classList.add("show");
            detailsArr[objHover].classList.remove("hide");
          } else {
            // Hide all other pins
            pinsArr[objHover].classList.add("hide");
            pinsArr[objHover].classList.remove("show");
            // Hide all other details
            detailsArr[objHover].classList.add("hide");
            detailsArr[objHover].classList.remove("show");
          }
        }
        // Change width of object loader
        // document.getElementById("objectLoader")
        objRefList.style.width = "494px";
        // Change width of button
        objBtnRef = document.getElementById("objectLoader-btn") as HTMLElement;
        objBtnRef.style.width = "520px";
      }.bind(null, indexOfObj));

      // Hide all pins when not hovering on an object
      objListArr[indexOfObj].addEventListener('mouseout', function (indexOfObj) {
        for (objHover = 0; objHover < objListArr.length; objHover++) {
          // Hide all pins
          pinsArr[objHover].classList.add("hide");
          pinsArr[objHover].classList.remove("show");
          // Hide all detail
          detailsArr[objHover].classList.add("hide");
          detailsArr[objHover].classList.remove("show");
        }
        // Reset width of object loader
        // document.getElementById("objectLoader")
        objRefList.style.width = "286px";
        // Reset width of button
        // document.getElementById("objectLoader-btn")
        objBtnRef.style.width = "312px";

      }.bind(null, indexOfObj));
    }
    //////////////////// End of 2. Dropdown Menu: Object Loader ////////////////////
  }
  filterProjectObjects = () => {
    var input, filter, i, txtValue, object, objectRef, objectArr;
    input = document.getElementById("projectObjects") as HTMLElement;
    filter = input.value.toUpperCase();
    objectRef = document.getElementById("objectLoader") as HTMLElement;
    object = objectRef.querySelectorAll(".object")
    objectArr = Array.prototype.slice.call(object);

    for (i = 0; i < objectArr.length; i++) {
      txtValue = objectArr[i].textContent || objectArr[i].innerText;
      if (txtValue.toUpperCase().indexOf(filter) > -1) {
        objectArr[i].style.display = "";
      } else {
        objectArr[i].style.display = "none";
      }
    }
    //console.log(object);
  }
