import React, { Component } from 'react';
import MessageType from '../../../../models/MessageType';

export default class CamPathInspector extends Component<{ flag, cameraStatus, inspectorValue }>
{
    state = {
        camPathMemberloopCss: 'show',
        camPathMemberstopLoopCss: 'hide',
        camPathMemberlockCss: 'hide',
        camPathMemberunlockCss: 'show',
        camPathMemberplayCss: 'show',
        camPathMemberpauseCss: 'hide',
        camInspectorValue: {
            camPosX: '*',
            camPosY: '*',
            camPosZ: '*',
            camRotX: '*',
            camRotY: '*',
            camRotZ: '*',
            camScaleX: '*',
            camScaleY: '*',
            camScaleZ: '*',
            camSpeed: '*',
            camStopTime: '*',
        },
        camInspectorEditStatus: false,
        locked: false,
        Hidden: false,
        objectName: '',
        addClicked: false,
        removeClicked: false,
        playClicked: false,
        loopClicked: false,
        uploadClicked: false,
        cameraOnState:false,
        actionJson: '{"type": "TOUR_TOOL_INSPECTOR","value":{"action":""}}',
    };
    //----code for getting the value from property to state-------------
    static getDerivedStateFromProps(props, state) {
        let lockStatus = props.inspectorValue.locked;

        let cameraStatus = props.cameraStatus;


        if (state.playClicked == false) {
            if (cameraStatus != '') {


                if (cameraStatus == 'Stop') {

                    return {
                        camPathMemberplayCss: 'show',
                        camPathMemberpauseCss: 'hide',
                        cameraOnState:false,
                        playClicked: false,
                        state,
                    }
                }
            }
        }




        if (lockStatus != undefined) {

            let arr = [] as any;
            let jsonVal = props.inspectorValue;


            //console.log(jsonVal);

            Object.keys(jsonVal).forEach(function (key) {


                if (key === 'position' || key === 'rotation' || key === 'scale')
                    arr.push(key);

            });

            arr.forEach(element => {
                Object.keys(jsonVal[element]).forEach(function (key) {

                    if (jsonVal[element][key] != Math.floor(jsonVal[element][key])) {
                        let num = jsonVal[element][key].toFixed(2);
                        jsonVal[element][key] = parseFloat(num);

                    }
                });


            });




            //--------------------------------------------------------
            let camPathMemberloopCss = state.camPathMemberloopCss;
            let camPathMemberstopLoopCss = state.camPathMemberstopLoopCss;

           

            if (jsonVal.loop == true) {
                camPathMemberloopCss = "hide";
                camPathMemberstopLoopCss = "show";
            }

            // camPathMemberlockCss ="hide";
            // camPathMemberunlockCss ="show";
            var  _camPathMemberlock = state.camPathMemberlockCss;
            var   _camPathMemberunlock = state.camPathMemberunlockCss;
            let locked=state.locked;
           if(jsonVal.locked==true)
           {
            _camPathMemberlock ="show";
            _camPathMemberunlock ="hide";
            locked =true;
           }
           let Hidden= state.Hidden;
			if(jsonVal.hidden==true)
			{
				Hidden=true;
			}
            //--------------------------------------------------------------------  
            if (state.camPathMemberlockCss == '' && state.camPathMemberunlockCss == '') {
                if (lockStatus == true && state.camInspectorEditStatus == false) {

                    return {
                        camPathMemberlockCss: "show",
                        camPathMemberunlockCss: "hide",
                        camInspectorValue:
                        {
                            camPosX: jsonVal.position.x,
                            camPosY: jsonVal.position.y,
                            camPosZ: jsonVal.position.z,
                            camRotX: jsonVal.rotation.x,
                            camRotY: jsonVal.rotation.y,
                            camRotZ: jsonVal.rotation.z,
                            camScaleX: jsonVal.scale.x,
                            camScaleY: jsonVal.scale.y,
                            camScaleZ: jsonVal.scale.z,
                            camSpeed: jsonVal.speed,
                            camStopTime: jsonVal.stopTime,

                        },
                        camPathMemberloopCss: camPathMemberloopCss,
                        camPathMemberstopLoopCss: camPathMemberstopLoopCss,
                        objectName: jsonVal.objectName,

                    };
                }
                if (lockStatus == true && state.camInspectorEditStatus == true) {

                    return {
                        camInspectorEditStatus: false,

                        state


                    };
                }

                if (lockStatus == false && state.camInspectorEditStatus == false) {
                    return {
                        camPathMemberlockCss: "hide",
                        camPathMemberunlockCss: "show",
                        camInspectorValue:
                        {
                            camPosX: jsonVal.position.x,
                            camPosY: jsonVal.position.y,
                            camPosZ: jsonVal.position.z,
                            camRotX: jsonVal.rotation.x,
                            camRotY: jsonVal.rotation.y,
                            camRotZ: jsonVal.rotation.z,
                            camScaleX: jsonVal.scale.x,
                            camScaleY: jsonVal.scale.y,
                            camScaleZ: jsonVal.scale.z,
                            camSpeed: jsonVal.speed,
                            camStopTime: jsonVal.stopTime,

                        },
                        camPathMemberloopCss: camPathMemberloopCss,
                        camPathMemberstopLoopCss: camPathMemberstopLoopCss,


                        objectName: jsonVal.objectName,
                    };
                }
                if (lockStatus == false && state.camInspectorEditStatus == true) {
                    return {
                        camInspectorEditStatus: false,
                        state

                    };

                }


            }

            if (state.camInspectorEditStatus == false) {
                return {

                    camInspectorValue:
                    {
                        camPosX: jsonVal.position.x,
                        camPosY: jsonVal.position.y,
                        camPosZ: jsonVal.position.z,
                        camRotX: jsonVal.rotation.x,
                        camRotY: jsonVal.rotation.y,
                        camRotZ: jsonVal.rotation.z,
                        camScaleX: jsonVal.scale.x,
                        camScaleY: jsonVal.scale.y,
                        camScaleZ: jsonVal.scale.z,
                        camSpeed: jsonVal.speed,
                        camStopTime: jsonVal.stopTime,
                        
                    },
                    camPathMemberloopCss: camPathMemberloopCss,
                    camPathMemberstopLoopCss: camPathMemberstopLoopCss,
                    camPathMemberlockCss :_camPathMemberlock,
                    camPathMemberunlockCss :_camPathMemberunlock,
                    objectName: jsonVal.objectName,
                    playClicked: false,
                    locked:locked,
                    Hidden:Hidden,
                };
            }
            if (state.camInspectorEditStatus == true) {

                return {
                    camInspectorEditStatus: false,
                    playClicked: false,
                    state


                };
            }

        }

        return null;
        // basicLockStatus: lockStatus,  	  

    }
    //-------code for filter the inpute entered by user as real number and also convert to float at the 
    //time of json creatation-----------------------------------------------------------
    getJsonFormat(jsonValue) {
        let jsonArr = jsonValue;
        //console.log(e.target.value);
        let arr = [] as any;
        //console.log(jsonVal);

        Object.keys(jsonArr).forEach(function (key) {

            arr.push(key);
        });
        let patt = /-?\d*\.{0,1}\d+$/;
        arr.forEach(element => {
            // if (jsonArr[element] == '') {
            // 	jsonArr[element] = 0;
            // }
            // else 
            if (patt.test(jsonArr[element])) {
                jsonArr[element] = parseFloat(jsonArr[element]);
            }
            else {
                jsonArr[element] = 0;
            }

        });

        //	console.log(jsonVal);


        this.setState({
            camInspectorValue: jsonArr,
        });
    }
    //---getting the input on keypress and set back to input field-------------------
    getValue = (em) => {
        let currentVal = em.target.value;
        let idVal = em.target.id;
        let jsonArr = this.state.camInspectorValue;
        let patt = /^[0-9\.\-\/]+$/;
        if (patt.test(currentVal) || currentVal == '') {
            if (currentVal == '-' || jsonArr[idVal] === '.') {
                jsonArr[idVal] = currentVal;
            }
            else if (currentVal != '') {

                jsonArr[idVal] = currentVal;
            }
            else if (currentVal == '') {
                jsonArr[idVal] = currentVal;
            }

            // else if(jsonArr[idVal]==='-' || jsonArr[idVal]==='.') {
            // 	jsonArr[idVal] = currentVal;
            // }

            this.setState({
                camInspectorValue: jsonArr,
                camInspectorEditStatus: true,


            });
        }



    }
    //----Responsible for handling the event on enter press from keyboard---------
    handleKeyDown = (e) => {

        // let currentVal = e.target.value;
        // let idVal = e.target.id;
        if (e.key === 'Enter') {



            this.setState({
                camInspectorEditStatus: true
            });


            this.getJsonFormat(this.state.camInspectorValue);


            let jsoninspectorValue = {
                "position": {
                    "x": this.state.camInspectorValue.camPosX,
                    "y": this.state.camInspectorValue.camPosY, "z": this.state.camInspectorValue.camPosZ
                },
                "rotation": {
                    "x": this.state.camInspectorValue.camRotX,
                    "y": this.state.camInspectorValue.camRotY,
                    "z": this.state.camInspectorValue.camRotZ
                },
                "scale": {
                    "x": this.state.camInspectorValue.camScaleX,
                    "y": this.state.camInspectorValue.camScaleY,
                    "z": this.state.camInspectorValue.camScaleZ
                },
                "locked": this.state.locked, "Hidden": this.state.Hidden,
                "speed": this.state.camInspectorValue.camSpeed, "stopTime": this.state.camInspectorValue.camStopTime,

            };


            console.log(jsoninspectorValue);


            if (window.vuplex) {
                window.vuplex.postMessage({ type: MessageType.TOUR_TOOL_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
            }
        }//end of enter key code






    }
    //----------code for handling the various button press by the user like forward,backward etc. ------------
    clickHandler = (e) => {

        this.setState({ camInspectorEditStatus: true, });



        let actionJson = JSON.parse(this.state.actionJson);
        let actionEvent = '';
        if (e.currentTarget.id == MessageType[e.currentTarget.id]) {
            actionEvent = MessageType[e.currentTarget.id];
        }
        actionJson.value["action"] = actionEvent;



      //  console.log(actionJson);
        if (window.vuplex) {
            window.vuplex.postMessage({ type: MessageType.TOUR_TOOL_INSPECTOR, value: JSON.stringify(actionJson) });
        }
    }

    //--------code for handling the event of play button pressed----------------
    camPlayToggle = () => {


        let actionJson = JSON.parse(this.state.actionJson);
        let actionEvent = '';

        if (this.state.camInspectorEditStatus == true) {
            this.getJsonFormat(this.state.camInspectorValue);
        }
        this.setState({ camInspectorEditStatus: true, playClicked: true,cameraOnState:true, });
        // let jsoninspectorValue = {
        //     "position":
        //     {
        //         "x": this.state.camInspectorValue.camPosX != '*' ? this.state.camInspectorValue.camPosX : this.props.inspectorValue.position.x,

        //         "y": this.state.camInspectorValue.camPosY != '*' ? this.state.camInspectorValue.camPosY : this.props.inspectorValue.position.y,

        //         "z": this.state.camInspectorValue.camPosZ != '*' ? this.state.camInspectorValue.camPosZ : this.props.inspectorValue.position.z
        //     },

        //     "rotation": {
        //         "x": this.state.camInspectorValue.camRotX != '*' ? this.state.camInspectorValue.camRotX : this.props.inspectorValue.rotation.x,

        //         "y": this.state.camInspectorValue.camRotY != '*' ? this.state.camInspectorValue.camRotY : this.props.inspectorValue.rotation.y,

        //         "z": this.state.camInspectorValue.camRotZ != '*' ? this.state.camInspectorValue.camRotZ : this.props.inspectorValue.rotation.z
        //     },

        //     "scale": {
        //         "x": this.state.camInspectorValue.camScaleX != '*' ? this.state.camInspectorValue.camScaleX : this.props.inspectorValue.scale.x,

        //         "y": this.state.camInspectorValue.camScaleY != '*' ? this.state.camInspectorValue.camScaleY : this.props.inspectorValue.scale.y,

        //         "z": this.state.camInspectorValue.camScaleZ != '*' ? this.state.camInspectorValue.camScaleZ : this.props.inspectorValue.scale.z
        //     },

        //     "locked": locked, "Hidden": this.state.Hidden,


        // };

        if (this.state.camPathMemberplayCss === "show") {
            actionEvent = "PLAY_NODE";
            this.setState({
                camPathMemberplayCss: 'hide',
                camPathMemberpauseCss: 'show',

            });
        }
        else {
            actionEvent = "PAUSE_NODE";
            this.setState({
                camPathMemberplayCss: 'show',
                camPathMemberpauseCss: 'hide',




            });
        }
        actionJson.value["action"] = actionEvent;

        console.log(actionJson);


        if (window.vuplex) {
            window.vuplex.postMessage({ type: MessageType.TOUR_TOOL_INSPECTOR, value: JSON.stringify(actionJson) });
        }




    }
    //----------code for handling the event for loop button pressed------------
    camLoopToggle = () => {


        let actionJson = JSON.parse(this.state.actionJson);
        let actionEvent = '';

        if (this.state.camInspectorEditStatus == true) {
            this.getJsonFormat(this.state.camInspectorValue);
        }

        this.setState({ camInspectorEditStatus: true });

        if (this.state.camPathMemberloopCss === "show") {
            actionEvent = "LOOP_NODE";
            this.setState({
                camPathMemberloopCss: 'hide',
                camPathMemberstopLoopCss: 'show',
            });
        }
        else {
            actionEvent = "UNLOOP_NODE";
            this.setState({
                camPathMemberloopCss: 'show',
                camPathMemberstopLoopCss: 'hide',
            });
        }
        actionJson.value["action"] = actionEvent;

        console.log(actionJson);


        if (window.vuplex) {
            window.vuplex.postMessage({ type: MessageType.TOUR_TOOL_INSPECTOR, value: JSON.stringify(actionJson) });
        }




    }
    //------------code for handling the event for lock button pressed---------
    camLockToggle = (event) => {


        if (this.state.camInspectorEditStatus == true) {
            this.getJsonFormat(this.state.camInspectorValue);
        }
        this.setState({ camInspectorEditStatus: true });
        let locked = false;
        let jsoninspectorValue = {
            "position":
            {
                "x": this.state.camInspectorValue.camPosX != '*' ? this.state.camInspectorValue.camPosX : this.props.inspectorValue.position.x,

                "y": this.state.camInspectorValue.camPosY != '*' ? this.state.camInspectorValue.camPosY : this.props.inspectorValue.position.y,

                "z": this.state.camInspectorValue.camPosZ != '*' ? this.state.camInspectorValue.camPosZ : this.props.inspectorValue.position.z
            },

            "rotation": {
                "x": this.state.camInspectorValue.camRotX != '*' ? this.state.camInspectorValue.camRotX : this.props.inspectorValue.rotation.x,

                "y": this.state.camInspectorValue.camRotY != '*' ? this.state.camInspectorValue.camRotY : this.props.inspectorValue.rotation.y,

                "z": this.state.camInspectorValue.camRotZ != '*' ? this.state.camInspectorValue.camRotZ : this.props.inspectorValue.rotation.z
            },

            "scale": {
                "x": this.state.camInspectorValue.camScaleX != '*' ? this.state.camInspectorValue.camScaleX : this.props.inspectorValue.scale.x,

                "y": this.state.camInspectorValue.camScaleY != '*' ? this.state.camInspectorValue.camScaleY : this.props.inspectorValue.scale.y,

                "z": this.state.camInspectorValue.camScaleZ != '*' ? this.state.camInspectorValue.camScaleZ : this.props.inspectorValue.scale.z
            },

            "locked": locked, "Hidden": this.state.Hidden,
            "speed": this.state.camInspectorValue.camSpeed, "stopTime": this.state.camInspectorValue.camStopTime,

        };

        if (this.state.camPathMemberlockCss === "hide") {
            locked = true;
            this.setState({
                camPathMemberlockCss: "show",
                camPathMemberunlockCss: "hide",
                locked: locked,
            });
            //lock status  value updated in Unity json...
            jsoninspectorValue["locked"] = locked;



            console.log(jsoninspectorValue);


            if (window.vuplex) {
                window.vuplex.postMessage({ type: MessageType.TOUR_TOOL_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
            }
        }

        else {
            locked = false;
            this.setState({
                camPathMemberlockCss: "hide",
                camPathMemberunlockCss: "show",
                locked: locked,
            });
            //lock status  value updated in Unity json...
            jsoninspectorValue["locked"] = locked;

            console.log(jsoninspectorValue);


            if (window.vuplex) {
                window.vuplex.postMessage({ type: MessageType.TOUR_TOOL_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
            }

        }

    }
    hiddenToggle = (event) =>{
		let Hidden = false;
		if(this.state.camInspectorEditStatus == true )
		{
			this.getJsonFormat(this.state.camInspectorValue);
        }
        this.setState({camInspectorEditStatus:true,});
        let jsoninspectorValue = {
            "position":
            {
                "x": this.state.camInspectorValue.camPosX != '*' ? this.state.camInspectorValue.camPosX : this.props.inspectorValue.position.x,

                "y": this.state.camInspectorValue.camPosY != '*' ? this.state.camInspectorValue.camPosY : this.props.inspectorValue.position.y,

                "z": this.state.camInspectorValue.camPosZ != '*' ? this.state.camInspectorValue.camPosZ : this.props.inspectorValue.position.z
            },

            "rotation": {
                "x": this.state.camInspectorValue.camRotX != '*' ? this.state.camInspectorValue.camRotX : this.props.inspectorValue.rotation.x,

                "y": this.state.camInspectorValue.camRotY != '*' ? this.state.camInspectorValue.camRotY : this.props.inspectorValue.rotation.y,

                "z": this.state.camInspectorValue.camRotZ != '*' ? this.state.camInspectorValue.camRotZ : this.props.inspectorValue.rotation.z
            },

            "scale": {
                "x": this.state.camInspectorValue.camScaleX != '*' ? this.state.camInspectorValue.camScaleX : this.props.inspectorValue.scale.x,

                "y": this.state.camInspectorValue.camScaleY != '*' ? this.state.camInspectorValue.camScaleY : this.props.inspectorValue.scale.y,

                "z": this.state.camInspectorValue.camScaleZ != '*' ? this.state.camInspectorValue.camScaleZ : this.props.inspectorValue.scale.z
            },

            "locked": this.state.locked, "Hidden": this.state.Hidden,
            "speed": this.state.camInspectorValue.camSpeed, "stopTime": this.state.camInspectorValue.camStopTime,

        };
		if(this.state.Hidden==true)
		{
			Hidden = false;
			this.setState({
				
				Hidden:Hidden,
			});

		    jsoninspectorValue["Hidden"] = Hidden;
		
            if (window.vuplex) {
                window.vuplex.postMessage({ type: MessageType.TOUR_TOOL_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
            }
            console.log(jsoninspectorValue);
		}
		else
		{
			Hidden =true;
			this.setState({
				
				Hidden:Hidden,
			});
            jsoninspectorValue["Hidden"] = Hidden;
			
            console.log(jsoninspectorValue);
            if (window.vuplex) {
                window.vuplex.postMessage({ type: MessageType.TOUR_TOOL_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
            }
		}





	}
    //--------Responsible for rendering the element in the browser----------
    render() {
        return (

            <div className="camPathMember-inspector">
                <div id="camPathMember" className={"camPathMember-wrapper " + this.props.flag}>
                    {/* <!-- Functionality --> */}
                    <div className="camPathFn-outerContainer">
                        <div className="camPathFn-innerContainer">
                            {/* <!-- Add --> */}
                            {this.state.cameraOnState==false && this.state.locked==false?
                                <div className="camPathMemberFn">
                                <span id="ADD_NODE" onClick={this.clickHandler} className="left pointer" ><svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="13" cy="18" r="13" fill="#4d4d4d" /><g className="btn-icon-stroke-dark"><line x1="7.31" y1="18" x2="18.69" y2="18" fill="none" stroke-miterlimit="10" stroke-width="2" /><line x1="13" y1="23.69" x2="13" y2="12.31" fill="none" stroke-miterlimit="10" stroke-width="2" /></g></svg></span>
                                <h3 className="left">add tour stop</h3>
                                </div>

                            :
                                <div className="camPathMemberFn">
                                <span  id="ADD_NODE" onClick={this.clickHandler} className="left"><svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none"/><circle cx="13" cy="18" r="13" fill="#4d4d4d"/><g className="btn-icon-stroke-dark-disabled"><line x1="7.31" y1="18" x2="18.69" y2="18" fill="none" stroke-miterlimit="10" stroke-width="2"/><line x1="13" y1="23.69" x2="13" y2="12.31" fill="none" stroke-miterlimit="10" stroke-width="2"/></g></svg></span>
                                <h3 className="left disabled">add tour stop</h3>
                            </div>
                            }
                           
                      
                            {/* <!-- Remove --> */}
                            {this.state.cameraOnState==false && this.state.locked==false?
                            <div className="camPathMemberFn">
                                <span id="REMOVE_NODE" onClick={this.clickHandler} className="left pointer"><svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="13" cy="18" r="13" fill="#4d4d4d" /><line className="btn-icon-stroke-dark" x1="7.31" y1="18" x2="18.69" y2="18" fill="none" stroke-miterlimit="10" stroke-width="2" /></svg></span>
                                <h3 className="left">remove tour stop</h3>
                            </div>
                            :
                            <div className="camPathMemberFn">
								<span id="REMOVE_NODE" onClick={this.clickHandler} className="left">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none"/><circle cx="13" cy="18" r="13" fill="#4d4d4d"/><line className="btn-icon-stroke-dark-disabled" x1="7.31" y1="18" x2="18.69" y2="18" fill="none" stroke-miterlimit="10" stroke-width="2"/></svg></span>
								<h3 className="left disabled">remove tour stop</h3>
							</div>
                            }
                            {/* <!-- Reset --> */}
                            {this.state.cameraOnState==false && this.state.locked==false?
                            <div className="camPathMemberFn">
                                <span id="RESET_NODE" onClick={this.clickHandler} className="left pointer"><svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="13" cy="18" r="13" fill="#4d4d4d" /><path className="btn-icon-stroke-dark" d="M18.67,17.6a5.69,5.69,0,1,1-2.83-4.53" fill="none" stroke-miterlimit="10" stroke-width="1.46" /><polygon className="btn-icon-fill-dark" points="16.86 10.6 14.21 15.2 17.84 14.22 16.86 10.6" /></svg></span>
                                <h3 className="left">reset tour stop view</h3>
                            </div>
                            :
                            <div className="camPathMemberFn">
								<span id="RESET_NODE" onClick={this.clickHandler} className="left"><svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none"/><circle cx="13" cy="18" r="13" fill="#4d4d4d"/><path className="btn-icon-stroke-dark-disabled" d="M18.67,17.6a5.69,5.69,0,1,1-2.83-4.53" fill="none" stroke-miterlimit="10" stroke-width="1.46"/><polygon className="btn-icon-fill-dark-disabled" points="16.86 10.6 14.21 15.2 17.84 14.22 16.86 10.6"/></svg></span>
								<h3 className="left disabled">reset tour stop view</h3>
							</div>

                            }
                            {/* <!-- Speed & Stop Time --> */}
                            <div className="camPathMemberInput-row-one">
                            {this.state.cameraOnState==false && this.state.locked==false?
                                <div className="camPathMemberFnInputWrap left">
                                    <h3 className="left">speed</h3>
                                    <input className="left"  id="camSpeed" type="number" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camSpeed} onChange={this.getValue} step=".01" title="Speed" />
                                </div>
                                :
                                <div className="camPathMemberFnInputWrap left">
									<h3 className="left disabled">speed</h3>
                                    <input className="left disabled" disabled type="number" value={this.state.camInspectorValue.camSpeed}  step=".01" title="Speed" />
								</div>
                               }
                                {/* <!-- Stop Time --> */}
                                {this.state.cameraOnState==false && this.state.locked==false?
                                <div className="camPathMemberFnInputWrap right">
                                    <h3 className="left">stop time (s)</h3>
                                    <input className="left"  id="camStopTime" type="number" step=".01" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camStopTime} onChange={this.getValue} title="Stop Time" />
                                </div>
                                :
                                <div className="camPathMemberFnInputWrap right">
									<h3 className="left disabled">stop time (s)</h3>
									<input className="left disabled" disabled  type="number" step=".01"  value={this.state.camInspectorValue.camStopTime} title="Stop Time" />
								</div>
                                 }
                            </div>
                        </div>
                    </div>
                    <hr className="camPathMember-rowTop" />
                    {/* <!-- Cam Path Tools: Row 1 --> */}
                    <div className="camPathMember-row-tools-one">
                        {/* <!-- Back --> */}
                        {this.state.cameraOnState==false && this.state.locked==false?
                            <span id="BACKWARD_NODE" onClick={this.clickHandler} className="left pointer">
                            <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">
                                <circle cx="18" cy="18" r="18" fill="none" /><polyline className="btn-icon-stroke-dark" points="25.58 10 10.42 18 25.58 26" fill="none" stroke-miterlimit="10" stroke-width="3" />
                            </svg>
                        </span>
                        :
                        <span id="BACKWARD_NODE" onClick={this.clickHandler} className="left">
                        <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">
                            <circle cx="18" cy="18" r="18" fill="none"/><polyline 
                            className="btn-icon-stroke-dark-disabled"
                             points="25.58 10 10.42 18 25.58 26" fill="none"
                             stroke-miterlimit="10" stroke-width="3"/>
                        </svg>
                    </span>
                        }
                    
                       
                        {/* <!-- Instructions --> */}
                        <span className="left navigate">
                            <h3>Navigate</h3>
                        </span>
                        {/* <!-- Forward --> */}
                        {this.state.cameraOnState==false && this.state.locked==false?
                        <span id="FORWARD_NODE" onClick={this.clickHandler} className="left pointer">
                            <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">
                                <circle cx="18" cy="18" r="18" fill="none" /><polyline className="btn-icon-stroke-dark" points="10.42 26 25.58 18 10.42 10" fill="none" stroke-miterlimit="10" stroke-width="3" />
                            </svg>
                        </span>
                        
                        :
                        <span className="left">
							<svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">
								<circle cx="18" cy="18" r="18" fill="none"/><polyline className="btn-icon-stroke-dark-disabled" points="10.42 26 25.58 18 10.42 10" fill="none" stroke-miterlimit="10" stroke-width="3"/>
							</svg>
						</span>
                    }
                    </div>
                    <hr className="camPathMember-rowTop" />
                    {/* <!-- Cam Path Tools: Row 2 --> */}
                    <div className="camPathMember-row-tools">
                        {/* <!-- Back To Start --> */}
                        {this.state.cameraOnState==false && this.state.locked==false?

                        <span id="START_NODE" onClick={this.clickHandler} className="left pointer">
                            <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><polygon className="btn-icon-fill-dark" points="26.61 26 26.61 10 12.39 17.53 12.39 10 12.3 10 10.17 10 9.39 10 9.39 26 10.17 26 12.3 26 12.39 26 12.39 18.47 26.61 26" /></svg>
                        </span>
                        :
                        <span className="left">
							<svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none"/><polygon className="btn-icon-fill-dark-disabled" points="26.61 26 26.61 10 12.39 17.53 12.39 10 12.3 10 10.17 10 9.39 10 9.39 26 10.17 26 12.3 26 12.39 26 12.39 18.47 26.61 26"/></svg>
						</span>
                        }

                        {/* <!-- Play/Pause --> onclick="camPathMemberPlayToggle()"*/}
                        <span className="left pointer" onClick={this.state.locked?undefined:this.camPlayToggle} >
                            <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">
                                <circle cx="18" cy="18" r="18" fill="none" />
                                {/* <!-- Play --> */}
                                <polygon id="camPathMember-play" className={"btn-icon-fill-dark " + this.state.camPathMemberplayCss} points="25.55 18 10.45 10 10.45 26 25.55 18" />
                                {/* <!-- Pause --> */}
                                <g id="camPathMember-pause" className={"btn-icon-fill-dark hide " + this.state.camPathMemberpauseCss}><rect x="12.4" y="10" width="3" height="16" /><rect x="20.6" y="10" width="3" height="16" /></g>
                            </svg>
                        </span>
                        {/* <!-- Loop/Stop Loop -->  onclick="camPathMemberLoopToggle()"*/}
                        {this.state.cameraOnState==false && this.state.locked==false?
                        <span onClick={this.camLoopToggle} className="left pointer">
                            <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">
                                <circle cx="18" cy="18" r="18" fill="none" />
                                {/* <!-- Loop --> */}
                                <path id="camPathMember-loop" className={"btn-icon-fill-dark " + this.state.camPathMemberloopCss} d="M30.61,17.37a8,8,0,0,0-8.13-7.46h-8l-.14-.22,1.4-1-.92-.9c-.65.65-1.26,1.28-1.9,1.88a1.4,1.4,0,0,1-.42.28H12a8.08,8.08,0,0,0-6.64,8,7.92,7.92,0,0,0,8,8.17h7.67c.19,0,.37,0,.56,0l.09.16c-.44.37-.89.74-1.29,1.09l.87.91.08-.09.09.09c.62-.64,1.23-1.29,1.88-1.91a1.57,1.57,0,0,1,.75-.4A7.94,7.94,0,0,0,30.61,17.37Zm-4.05,6.25A16.46,16.46,0,0,1,24,24.74a.53.53,0,0,1-.65-.19c-.64-.67-1.3-1.33-2-2l-.08.09-.09-.09-.88,1,1.59,1.37-.37.05c-2.69,0-5.38,0-8.07,0a6.48,6.48,0,0,1-6-3.62,6.73,6.73,0,0,1,.38-7.18,6.65,6.65,0,0,1,4.23-2.85.64.64,0,0,1,.69.22c.62.66,1.27,1.3,1.91,2l.84-.91-1.43-1.23.1-.15a1.55,1.55,0,0,1,.21,0c2.8,0,5.61,0,8.42,0a6.76,6.76,0,0,1,6.33,4.82A6.84,6.84,0,0,1,26.56,23.62Z" />
                                {/* <!-- Stop Loop --> */}
                                <path id="camPathMember-stopLoop" className={"btn-icon-fill-dark hide " + this.state.camPathMemberstopLoopCss} d="M28.05,6.33,24.28,10.1a8.86,8.86,0,0,0-1.8-.19h-8l-.14-.22,1.4-1-.92-.9c-.65.65-1.26,1.28-1.9,1.88a1.4,1.4,0,0,1-.42.28H12a8.08,8.08,0,0,0-6.64,8,7.94,7.94,0,0,0,4,7.12L6.83,27.55,8.25,29,29.46,7.75ZM7.49,21.26a6.73,6.73,0,0,1,.38-7.18,6.65,6.65,0,0,1,4.23-2.85.64.64,0,0,1,.69.22c.62.66,1.27,1.3,1.91,2l.84-.91-1.43-1.23.1-.15a1.55,1.55,0,0,1,.21,0c2.8,0,5.61,0,8.42,0a2.85,2.85,0,0,1,.41,0l-13,13A6.47,6.47,0,0,1,7.49,21.26Z" /><path className="btn-icon-fill-dark" d="M30.61,17.37a8,8,0,0,0-3.36-6l-.85.85a6.68,6.68,0,0,1,2.77,3.69,6.84,6.84,0,0,1-2.61,7.71A16.46,16.46,0,0,1,24,24.74a.53.53,0,0,1-.65-.19c-.64-.67-1.3-1.33-2-2l-.08.09-.09-.09-.88,1,1.59,1.37-.37.05c-2.6,0-5.21,0-7.81,0L12.6,26a6.79,6.79,0,0,0,.78,0h7.67c.19,0,.37,0,.56,0l.09.16c-.44.37-.89.74-1.29,1.09l.87.91.08-.09.09.09c.62-.64,1.23-1.29,1.88-1.91a1.57,1.57,0,0,1,.75-.4A7.94,7.94,0,0,0,30.61,17.37Z" />
                            </svg>
                        </span>
                        :
                        <span className="left">
							<svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">
								<circle cx="18" cy="18" r="18" fill="none"/>
								
								<path id="camPathMember-loop" className="btn-icon-fill-dark-disabled" d="M30.61,17.37a8,8,0,0,0-8.13-7.46h-8l-.14-.22,1.4-1-.92-.9c-.65.65-1.26,1.28-1.9,1.88a1.4,1.4,0,0,1-.42.28H12a8.08,8.08,0,0,0-6.64,8,7.92,7.92,0,0,0,8,8.17h7.67c.19,0,.37,0,.56,0l.09.16c-.44.37-.89.74-1.29,1.09l.87.91.08-.09.09.09c.62-.64,1.23-1.29,1.88-1.91a1.57,1.57,0,0,1,.75-.4A7.94,7.94,0,0,0,30.61,17.37Zm-4.05,6.25A16.46,16.46,0,0,1,24,24.74a.53.53,0,0,1-.65-.19c-.64-.67-1.3-1.33-2-2l-.08.09-.09-.09-.88,1,1.59,1.37-.37.05c-2.69,0-5.38,0-8.07,0a6.48,6.48,0,0,1-6-3.62,6.73,6.73,0,0,1,.38-7.18,6.65,6.65,0,0,1,4.23-2.85.64.64,0,0,1,.69.22c.62.66,1.27,1.3,1.91,2l.84-.91-1.43-1.23.1-.15a1.55,1.55,0,0,1,.21,0c2.8,0,5.61,0,8.42,0a6.76,6.76,0,0,1,6.33,4.82A6.84,6.84,0,0,1,26.56,23.62Z"/>
								
								<path id="camPathMember-stopLoop" className="btn-icon-fill-dark-disabled hide" d="M28.05,6.33,24.28,10.1a8.86,8.86,0,0,0-1.8-.19h-8l-.14-.22,1.4-1-.92-.9c-.65.65-1.26,1.28-1.9,1.88a1.4,1.4,0,0,1-.42.28H12a8.08,8.08,0,0,0-6.64,8,7.94,7.94,0,0,0,4,7.12L6.83,27.55,8.25,29,29.46,7.75ZM7.49,21.26a6.73,6.73,0,0,1,.38-7.18,6.65,6.65,0,0,1,4.23-2.85.64.64,0,0,1,.69.22c.62.66,1.27,1.3,1.91,2l.84-.91-1.43-1.23.1-.15a1.55,1.55,0,0,1,.21,0c2.8,0,5.61,0,8.42,0a2.85,2.85,0,0,1,.41,0l-13,13A6.47,6.47,0,0,1,7.49,21.26Z"/><path className="btn-icon-fill-dark-disabled" d="M30.61,17.37a8,8,0,0,0-3.36-6l-.85.85a6.68,6.68,0,0,1,2.77,3.69,6.84,6.84,0,0,1-2.61,7.71A16.46,16.46,0,0,1,24,24.74a.53.53,0,0,1-.65-.19c-.64-.67-1.3-1.33-2-2l-.08.09-.09-.09-.88,1,1.59,1.37-.37.05c-2.6,0-5.21,0-7.81,0L12.6,26a6.79,6.79,0,0,0,.78,0h7.67c.19,0,.37,0,.56,0l.09.16c-.44.37-.89.74-1.29,1.09l.87.91.08-.09.09.09c.62-.64,1.23-1.29,1.88-1.91a1.57,1.57,0,0,1,.75-.4A7.94,7.94,0,0,0,30.61,17.37Z"/>
							</svg>
						</span>
                        }
                        {/* <!-- Export --> */}
                        {this.state.cameraOnState==false && this.state.locked==false?

                        <span id="UPLOAD_NODE" onClick={this.clickHandler} className="left camPathMember-cell-onePlusHalf pointer">
                            <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><g className="btn-icon-stroke-dark"><rect x="10.6" y="19" width="14.8" height="9" fill="none" stroke-miterlimit="10" stroke-width="1.38" /><line x1="18" y1="23.11" x2="18" y2="10.23" fill="none" stroke-miterlimit="10" stroke-width="1.5" /></g><polygon className="btn-icon-fill-dark" points="21.31 14.11 18 10.8 14.69 14.11 14.69 11.31 18 8 21.31 11.31 21.31 14.11" /></svg>
                        </span>
                        :
                        <span className="left camPathMember-cell-onePlusHalf">
							<svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">
                                <circle cx="18" cy="18" r="18" fill="none"/>
                                <g className="btn-icon-stroke-dark-disabled">
                                    <rect x="10.6" y="19" width="14.8" height="9" fill="none" stroke-miterlimit="10" stroke-width="1.38"/>
                                    <line x1="18" y1="23.11" x2="18" y2="10.23" fill="none" stroke-miterlimit="10" stroke-width="1.5"/></g>
                                    <polygon className="btn-icon-fill-dark-disabled" 
                                    points="21.31 14.11 18 10.8 14.69 14.11 14.69 11.31 18 8 21.31 11.31 21.31 14.11"/></svg>
						</span>
                        }
                    </div>
                    <hr className="camPathMember-rowTop" />
                    {/* <!-- Asset Name & Icons --> */}
                    <div className="camPathMember-row-one">
                        {/* <!-- Asset Name --> */}
                        <h1 className="left">{this.state.objectName != '' ? this.state.objectName : 'TOUR TOOL INSPECTOR'}</h1>
                        {/* <!-- Show/Hide --> */}
                        <span className="right pointer" onClick={this.state.locked?undefined:this.hiddenToggle}><svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36"><defs></defs><path className="a" d="M7.22,10.74a12.92,12.92,0,0,0-2,4.95L11.06,7A12.82,12.82,0,0,0,7.22,10.74Z" /><path className="a" d="M28.77,25.28a13,13,0,0,0,2-4.84L25,28.94A13,13,0,0,0,28.77,25.28Z" /><path className="a" d="M29,11.17,15.79,30.81a13.07,13.07,0,0,0,3.64.11L30.5,14.5A12.5,12.5,0,0,0,29,11.17Z" /><path className="a" d="M25.26,7.23a12.15,12.15,0,0,0-1.09-.66L9.7,28c.33.27.67.52,1,.76a11.57,11.57,0,0,0,1.56.89L26.68,8.35A13,13,0,0,0,25.26,7.23Z" /><path className="a" d="M16.93,5.06,5.59,21.87a12.84,12.84,0,0,0,1.52,3.25L20.51,5.25A13.06,13.06,0,0,0,16.93,5.06Z" /></svg></span>
                        {/* <!-- Lock/Unlock Icon -->onclick="camPathMemberLockToggle()" */}
                        <span onClick={this.camLockToggle} className="right pointer" >
                            <svg xmlns="http://www.w3.org/2000/svg" width="36" height="36" viewBox="0 0 36 36">
                                {/* <!-- Locked --> */}
                                <path id="camPathMember-lock" className={"btn-icon-fill-dark " + this.state.camPathMemberlockCss} d="M13.63,16.16V14.29a4.2,4.2,0,0,1,1.95-3.65,4.14,4.14,0,0,1,2.92-.7,4.25,4.25,0,0,1,3.41,2.37,4,4,0,0,1,.46,1.86v2h.89c.37,0,.5.14.5.5v8.93c0,.37-.13.51-.5.51H12.74c-.36,0-.5-.14-.5-.51V16.66c0-.36.14-.5.5-.5Zm1.79,0h5.16V14.09a1.07,1.07,0,0,0,0-.25,2.58,2.58,0,0,0-5.13.41v1.9Z" />
                                {/* <!-- Unlocked --> */}
                                <path id="camPathMember-unlock" className={"btn-icon-fill-dark hide " + this.state.camPathMemberunlockCss} d="M24.33,19.1l-.63-.62.11-.12c.43-.43.87-.86,1.3-1.3a3.92,3.92,0,0,0,1-1.63,4.26,4.26,0,0,0-.74-4.09,4.13,4.13,0,0,0-2.55-1.57,4.28,4.28,0,0,0-2.69.3l1.43,1.43a2.58,2.58,0,0,1,2.5,4.05.7.7,0,0,1-.15.19l-.06.06h0l-.76.77-.64.63h0v0l-5.59-5.59c-.23-.21-.42-.2-.66,0L9.87,18c-.26.26-.26.46,0,.71l7.43,7.44c.26.26.46.26.71,0l6.32-6.32A.42.42,0,0,0,24.33,19.1Z" />
                            </svg>
                        </span>
                    </div>
                    {/* <!-- Position --> */}
                    <div className="camPathMember-row-half">
                        <div className="left camPathMember-cell-one pad-right-half"><h3>POS</h3></div>
                        <div className="left pad-right-half"><input id="camPosX" type="number" placeholder="X"  disabled = {this.state.locked?true:false}  step=".01" title="X" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camPosX} onChange={this.getValue} /></div>
                        <div className="left pad-right-half"><input id="camPosY" type="number" placeholder="Y"  disabled = {this.state.locked?true:false}  step=".01" title="Y" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camPosY} onChange={this.getValue} /></div>
                        <div className="left"><input id="camPosZ" type="number" placeholder="Z" step=".01" title="Z"  disabled = {this.state.locked?true:false}  onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camPosZ} onChange={this.getValue} /></div>
                    </div>
                    {/* <!-- Rotation --> */}
                    <div className="camPathMember-row-half">
                        <div className="left camPathMember-cell-one pad-right-half"><h3>ROT</h3></div>
                        <div className="left pad-right-half"><input id="camRotX" type="number" placeholder="X"  disabled = {this.state.locked?true:false}  step=".01" title="X" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camRotX} onChange={this.getValue} /></div>
                        <div className="left pad-right-half"><input id="camRotY" type="number" placeholder="Y"  disabled = {this.state.locked?true:false}  step=".01" title="Y" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camRotY} onChange={this.getValue} /></div>
                        <div className="left"><input id="camRotZ" type="number" placeholder="Z"  disabled = {this.state.locked?true:false}  step=".01" title="Z" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camRotZ} onChange={this.getValue} /></div>
                    </div>
                    {/* <!-- Scale --> */}
                    <div className="camPathMember-row-half">
                        <div className="left camPathMember-cell-one pad-right-half"><h3>SCALE</h3></div>
                        <div className="left pad-right-half"><input id="camScaleX" type="number" placeholder="X"  disabled = {this.state.locked?true:false}  step=".01" title="X" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camScaleX} onChange={this.getValue} /></div>
                        <div className="left pad-right-half"><input id="camScaleY" type="number" placeholder="Y"  disabled = {this.state.locked?true:false}  step=".01" title="Y" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camScaleY} onChange={this.getValue} /></div>
                        <div className="left"><input id="camScaleZ" type="number" placeholder="Z"  disabled = {this.state.locked?true:false}  step=".01" title="Z" onKeyDown={this.handleKeyDown} value={this.state.camInspectorValue.camScaleZ} onChange={this.getValue} /></div>
                    </div>
                    {/* <!-- Headings --> */}
                    <div className="camPathMember-row-half">
                        <div className="left camPathMember-cell-one pad-right-half">&nbsp;</div>
                        <div className="left camPathMember-cell-one pad-right-half"><h3>X</h3></div>
                        <div className="left camPathMember-cell-one pad-right-half"><h3>Y</h3></div>
                        <div className="left camPathMember-cell-one"><h3>Z</h3></div>
                    </div>
                </div>
            </div>





        );
    }
}