import React, { Component } from 'react';

import Intercom from 'intercom-react';
// import MessageType from '../../../../models/MessageType';

export default class InterCom extends Component<{ userName, userEmail }>{
  
   
    render () {
       let userEmailId =this.props.userEmail;
       let userName =this.props.userName;
        return (
            
                userEmailId!='' && userEmailId!=undefined && userName!='' && userName!=undefined?
            <Intercom
            // open
            appId="pvna7e37"
            user={{
            //   user_id: '9876',
              email:userEmailId ,
            //   created_at: 1234567890,
              name: userName,
            }}
            // onOpen={() => {}}
            // onClose={() => {}}
            // onUnreadCountChange={unreadCount => {}}
            // onInitialization={intercom => {}}
          />
          :null
        

        );
      }
}