    loadColourPicker = () => {
        // COLOUR PICKER
        const colCanvas = document.getElementById("colourPickerPanel") as HTMLCanvasElement;
        var colCtx;

        if (colCanvas != null) {
            colCtx = colCanvas.getContext("2d");
        }

        if (colCtx != null) {
            // Create a colour gradient
            var colPickGradient = colCtx.createLinearGradient(0, 169, 338, 169);
            colPickGradient.addColorStop(0, "rgb(255,0,0)");
            colPickGradient.addColorStop(0.17, "rgb(255,255,0)");
            colPickGradient.addColorStop(0.34, "rgb(0,255,0)");
            colPickGradient.addColorStop(0.51, "rgb(0,255,255)");
            colPickGradient.addColorStop(0.68, "rgb(0,0,255)");
            colPickGradient.addColorStop(0.85, "rgb(255,0,255)");
            colPickGradient.addColorStop(1, "rgb(255,0,0)");
            // Apply colour gradient to canvas
            colCtx.fillStyle = colPickGradient;
            colCtx.fillRect(0, 0, 338, 169);
            // Creae semi-transparent gradient (white to transparent to black)
            var semiTraGradient = colCtx.createLinearGradient(0, 0, 0, 169);
            semiTraGradient.addColorStop(0, "rgba(255,255,255,1)");
            semiTraGradient.addColorStop(0.5, "rgba(255,255,255,0)");
            semiTraGradient.addColorStop(0.5, "rgba(0,0,0,0)");
            semiTraGradient.addColorStop(1, "rgba(0,0,0,1)");
            // Apply semi-transparent gradient to canvas
            colCtx.fillStyle = semiTraGradient;
            colCtx.fillRect(0, 0, 338, 169);
        }
    }
    findPos = (obj) => {
        var left = 0;
        var top = 0;
        if (obj.offsetParent) {
            do {
                left += obj.offsetLeft;
                top += obj.offsetTop;
            } while (obj = obj.offsetParent);
            return {
                x: left, y: top
            };
        }
        return { x: 0, y: 0 };
    }
    handleMouseMove = (e) => {

        this.setState({mouseMoved:true});

        // OPACITY SLIDER
        var canvas = document.getElementById("colourPickerPanel") as HTMLCanvasElement;
        //var opacityCanvas = document.getElementById("volOpacityPanel") as HTMLCanvasElement;
        var ctx;
        if (canvas != null) {
            ctx = canvas.getContext("2d");

        }

        var position = this.findPos(canvas);
        var x = e.pageX - position.x;
        var y = e.pageY - position.y;

        var p = ctx.getImageData(x, y, 1, 1).data;
        var r = p[0], g = p[1], b = p[2];
        // console.log(p[0]+" "+p[1]+" "+p[2]);
        // var a = p[3] / 255;
        // $('#volR').html("R " + r);
        // $('#volG').html("G " + g);
        // $('#volB').html("B " + b);
        this.setState({
            red: "R " + r,
            blue: "B " + b,
            green: "G " + g,

        });
    }
