                        vPosZ: jsonVal.position.z,
                        vRotX: jsonVal.rotation.x,
                        vRotY: jsonVal.rotation.y,
                        vRotZ: jsonVal.rotation.z,
                        vScaleX: jsonVal.scale.x,
                        vScaleY: jsonVal.scale.y,
