    //for the event of color picker
    LoadVolumetricColorPicker = () => {
        // COLOUR PICKER
        var canvas = document.getElementById("volColourPickerPanel") as HTMLCanvasElement;
        var ctx = canvas.getContext("2d");
        // Create a colour gradient
        if (ctx != null) {
            var colGradient = ctx.createLinearGradient(0, 169, 338, 169);
            colGradient.addColorStop(0, "rgb(255,0,0)");
            colGradient.addColorStop(0.17, "rgb(255,255,0)");
            colGradient.addColorStop(0.34, "rgb(0,255,0)");
            colGradient.addColorStop(0.51, "rgb(0,255,255)");
            colGradient.addColorStop(0.68, "rgb(0,0,255)");
            colGradient.addColorStop(0.85, "rgb(255,0,255)");
            colGradient.addColorStop(1, "rgb(255,0,0)");
            // Apply colour gradient to canvas
            ctx.fillStyle = colGradient;
            ctx.fillRect(0, 0, 338, 169);
            // Creae semi-transparent gradient (white to transparent to black)
            var transGradient = ctx.createLinearGradient(0, 0, 0, 169);
            transGradient.addColorStop(0, "rgba(255,255,255,1)");
            transGradient.addColorStop(0.5, "rgba(255,255,255,0)");
            transGradient.addColorStop(0.5, "rgba(0,0,0,0)");
            transGradient.addColorStop(1, "rgba(0,0,0,1)");
            // Apply semi-transparent gradient to canvas
            ctx.fillStyle = transGradient;
            ctx.fillRect(0, 0, 338, 169);
        }

    }

    findPos = (obj) => {
        var left = 0;
        var top = 0;
        if (obj.offsetParent) {
            do {
                left += obj.offsetLeft;
                top += obj.offsetTop;
            } while (obj = obj.offsetParent);
            return {
                x: left, y: top
            };
        }
        return { x: 0, y: 0 };
    }

    handleMouseMove = (e) => {
        // OPACITY SLIDER
        this.setState({mouseMoved:true});
        var canvas = document.getElementById("volColourPickerPanel") as HTMLCanvasElement;
        //var opacityCanvas = document.getElementById("volOpacityPanel") as HTMLCanvasElement;
        var ctx;
        if (canvas != null) {
            ctx = canvas.getContext("2d");

        }

        var position = this.findPos(canvas);
        var x = e.pageX - position.x;
        var y = e.pageY - position.y;

        var p = ctx.getImageData(x, y, 1, 1).data;
        var r = p[0], g = p[1], b = p[2];
        // console.log(p[0]+" "+p[1]+" "+p[2]);
        // var a = p[3] / 255;
        // $('#volR').html("R " + r);
        // $('#volG').html("G " + g);
        // $('#volB').html("B " + b);
        this.setState({
            red: "R " + r,
            blue: "B " + b,
            green: "G " + g,

        });
    }
