     rgbaToHex(color)
    { 
        let hexValue='';
       hexValue = rgbaConvert.hex(color);
        return hexValue;

    }
    //, nRow, nCol
    drawCheckeredBg(opacityCanvas) {

        var nRow, nCol;
        var context = opacityCanvas.getContext("2d");
        var width = opacityCanvas.width;
        var height = opacityCanvas.height;
        // # of rows and columns
        nRow = nRow || 2;
        nCol = nCol || 52;
        // Width and height of a square
        width /= nCol;
        height /= nRow;
        for (var i = 0; i < nRow; ++i) {
            for (var j = 0, col = nCol / 2; j < col; ++j) {
                context.rect(2 * j * width + (i % 2 ? 0 : width), i * height, width, height);
            }
        }
    }
