    //for the event of editing the values.
    getValue = (em) => {
        let currentVal = em.target.value;
        let idVal = em.target.id;
        let jsonArr = this.state.VolInspectorValue;
        let patt = /^[0-9\.\-\/]+$/;
        if (patt.test(currentVal) || currentVal == '') {
            if (currentVal == '-' || jsonArr[idVal] === '.') {
                jsonArr[idVal] = currentVal;
            }
            else if (currentVal != '') {

                jsonArr[idVal] = currentVal;
            }
            else if (currentVal == '') {
                jsonArr[idVal] = currentVal;
            }

            // else if(jsonArr[idVal]==='-' || jsonArr[idVal]==='.') {
            // 	jsonArr[idVal] = currentVal;
            // }

            this.setState({
                VolInspectorValue: jsonArr,
                volumetricInspectorEditStatus: true,

            });
        }



    }
