import React from "react";
import './style.scss';
import MessageType from "../../../models/MessageType";

function ToolsDropdown(props)
{
    

function getEventOnButtons(e){
    console.log(e.currentTarget.getAttribute('id').toUpperCase());
    let josnData = JSON.parse(props.toolsData);

    
   
    if(e.currentTarget.getAttribute('id').toUpperCase() === MessageType.MEDIAQUAD )
    { 
   

    if (window.vuplex) {
        window.vuplex.postMessage({ type: MessageType.MEDIAQUAD, value: JSON.stringify(josnData.obj_mediaquad) });
    }

      console.log(josnData.obj_mediaquad);
    }
    if(e.currentTarget.getAttribute('id').toUpperCase() === MessageType.POINTERS)
    {
        if (window.vuplex) {
            window.vuplex.postMessage({ type: MessageType.POINTERS, value: JSON.stringify(josnData.obj_pointers) });
        }
        console.log(josnData.obj_pointers);
    }
    if(e.currentTarget.getAttribute('id').toUpperCase() === MessageType.POV)
    {
        if (window.vuplex) {
            window.vuplex.postMessage({ type: MessageType.POINTERS, value: JSON.stringify(josnData.obj_pov) });
        }
    }
    if(e.currentTarget.getAttribute('id').toUpperCase() === MessageType.TEXT) 
    {
        if (window.vuplex) {
            window.vuplex.postMessage({ type: MessageType.POINTERS, value: JSON.stringify(josnData.obj_text) });
        }
    }
    if(e.currentTarget.getAttribute('id').toUpperCase() === MessageType.GRID)
    {
        if (window.vuplex) {
            window.vuplex.postMessage({ type: MessageType.POINTERS, value: JSON.stringify(josnData.obj_grid) });
        }
    }
    if(e.currentTarget.getAttribute('id').toUpperCase() === MessageType.TOURTOOL)
    {
        if (window.vuplex) {
            window.vuplex.postMessage({ type: MessageType.POINTERS, value: JSON.stringify(josnData.obj_tourtool) });
        }
        
    }
}

function toolBtnClick(e){
 getEventOnButtons(e);
    
   

}   
function onDragOver(ev)
{
  ev.preventDefault();
 
}
function onDragStart(ev)
{ 
    getEventOnButtons(ev);
}
return (   
     
    <div className="tools dropdown">
    {/* <!-- Button --> */}
    <button onClick={props.onBtnClick} id="tools-btn" className={"tools-dropdown-btn "+props.toolsbtnCss}>
        <div className="tools-states">
            <svg xmlns="http://www.w3.org/2000/svg" width="52" height="52" viewBox="0 0 52 52">
                {/* <!-- Opened State --> */}
                <g id="tools-opened" className={props.toolsopenedCss} >
                    <circle className="btn-bg" cx="26" cy="26" r="26"/>
                    <g className="btn-icon-fill">
                        <circle cx="20.5" cy="20.5" r="3.5"/>
                        <circle cx="31.5" cy="20.5" r="3.5"/>
                        <circle cx="20.5" cy="31.5" r="3.5"/>
                        <circle cx="31.5" cy="31.5" r="3.5"/>
                    </g>
                </g>
                {/* <!-- Closed State --> */}
                <g id="tools-closed" className={"hide btn-icon-stroke-dark "+props.toolsclosedCss }>
                    <line x1="19.25" y1="19.25" x2="32.75" y2="32.75" fill="none" stroke-miterlimit="10" stroke-width="2.5"/>
                    <line x1="19.25" y1="32.75" x2="32.75" y2="19.25" fill="none" stroke-miterlimit="10" stroke-width="2.5"/>
                </g>
            </svg>
        </div>
    </button>
    {/* <!-- /Button -->
    <!-- Content --> */}
    <div id="tools" className={"tools-content "+props.toolsCss}>
        <div id="toolsList" className="tools-wrapper">
        {/* <!-- To add more tools, add className="tool" to div. This is necessary for grid alignment and dropdown styling -->
            <!-- Tool 1: Media Object --> */}
            <div id="mediaquad" onClick={toolBtnClick} draggable  onDragStart={onDragStart} 
             onDragOver={onDragOver}
            className="tool">
                <svg  xmlns="http://www.w3.org/2000/svg" className="tool-icon" width="36" height="36" viewBox="0 0 36 36"><circle className="tool-icon-bg" cx="18" cy="18" r="18"/><polygon points="24.5 18 11.5 11.12 11.5 24.88 24.5 18" fill="#fff"/></svg>
            </div>
            {/* <!-- Tool 2: Measure --> */}
            <div  id="pointers" onClick={toolBtnClick} 
            draggable  onDragStart={onDragStart} 
            onDragOver={onDragOver}
            className="tool">
                <svg xmlns="http://www.w3.org/2000/svg" className="tool-icon" width="36" height="36" viewBox="0 0 36 36"><circle className="tool-icon-bg" cx="18" cy="18" r="18"/><path d="M9.2,22.55c.08.08.13.14.19.19.51.51,1,1,1.52,1.52a.5.5,0,1,0,.72-.69L10.1,22c0-.06-.11-.11-.18-.18l1.4-1.4a1.29,1.29,0,0,1,.17.16l1.56,1.55a.51.51,0,0,0,.61.12.47.47,0,0,0,.26-.54.91.91,0,0,0-.22-.33c-.49-.51-1-1-1.49-1.5L12,19.74l1.41-1.4a1.29,1.29,0,0,1,.17.16l2.24,2.23a.49.49,0,0,0,.66.13.48.48,0,0,0,.07-.8l-2.2-2.2-.23-.23c.47-.48.94-.94,1.42-1.42l.28.29,1.43,1.42a.5.5,0,1,0,.7-.7c-.56-.57-1.13-1.13-1.71-1.71l1.42-1.41c0,.06.11.11.17.17l1.52,1.52a.5.5,0,1,0,.72-.69l-1.54-1.53-.17-.18L19.79,12a1.53,1.53,0,0,1,.18.18l2.22,2.22a.51.51,0,0,0,.8,0,.45.45,0,0,0,0-.6,1.62,1.62,0,0,0-.18-.2l-2.14-2.14c-.06-.05-.11-.11-.18-.18l1.41-1.41.3.29,1.41,1.41a.48.48,0,0,0,.75,0,.48.48,0,0,0-.05-.73L22.62,9.16c.05-.06.09-.12.14-.17l.76-.75a.52.52,0,0,1,.88,0l3.36,3.36c.32.32.32.55,0,.86L12.45,27.77a.51.51,0,0,1-.86,0L8.23,24.4a.52.52,0,0,1,0-.86Z" fill="#fff" fill-rule="evenodd"/></svg>	
            </div>
            {/* <!-- Tool 3: POV --> */}
            <div id="pov" onClick={toolBtnClick} 
            draggable  onDragStart={onDragStart} 
            onDragOver={onDragOver}
             className="tool">
                <svg  xmlns="http://www.w3.org/2000/svg" className="tool-icon" width="36" height="36" viewBox="0 0 36 36"><circle className="tool-icon-bg" cx="18" cy="18" r="18" /><path d="M17.87,11.92a2.46,2.46,0,1,1,2.46-2.46A2.46,2.46,0,0,1,17.87,11.92Zm0-3.35a.89.89,0,1,0,.89.89A.88.88,0,0,0,17.87,8.57Z" fill="#fff"/><polygon points="18.66 17.78 20.73 26.03 19.2 26.41 17.9 21.24 16.7 26.4 15.17 26.04 17.09 17.82 17.09 14.23 16.75 14.23 15.29 17.53 13.86 16.89 15.73 12.66 20 12.66 22.09 16.61 20.7 17.35 19.05 14.23 18.66 14.23 18.66 17.78" fill="#fff"/><path d="M18,29c-2.33,0-6.28-.52-6.28-2.48,0-.58.37-1.38,2.12-1.93l.47,1.5a3.08,3.08,0,0,0-.94.43c.41.33,2,.91,4.63.91s4.22-.58,4.63-.91a3.32,3.32,0,0,0-1-.46l.46-1.5c1.81.55,2.2,1.37,2.2,2C24.28,28.48,20.33,29,18,29Z" fill="#fff"/></svg>
            </div>
            {/* <!-- Tool 4: Text --> */}
            <div id="text" onClick={toolBtnClick}
            draggable  onDragStart={onDragStart} 
            onDragOver={onDragOver}
            className="tool">
                <svg  xmlns="http://www.w3.org/2000/svg" className="tool-icon" width="36" height="36" viewBox="0 0 36 36"><circle className="tool-icon-bg" cx="18" cy="18" r="18"/><path d="M13.76,26.5V24.08h2.41V11.93H11.93v2.41H9.5V9.5h17v4.84H24.08v-2.4H19.83V24.07h2.41V26.5Z" fill="#fff"/></svg>
            </div>
            {/* <!-- Tool 5: Grid --> */}
            <div id="grid" onClick={toolBtnClick} 
            draggable  onDragStart={onDragStart} 
            onDragOver={onDragOver}
            className="tool">
                <svg  xmlns="http://www.w3.org/2000/svg" className="tool-icon" width="36" height="36" viewBox="0 0 36 36"><circle className="tool-icon-bg" cx="18" cy="18" r="18"/><line x1="15.57" y1="9.31" x2="11.55" y2="24.31" fill="none" stroke="#fff" stroke-miterlimit="10" stroke-width="1.15"/><line x1="24.45" y1="11.69" x2="20.43" y2="26.69" fill="none" stroke="#fff" stroke-miterlimit="10" stroke-width="1.15"/><line x1="20.01" y1="10.5" x2="15.99" y2="25.5" fill="none" stroke="#fff" stroke-miterlimit="10" stroke-width="1.15"/><line x1="26.69" y1="15.57" x2="11.69" y2="11.55" fill="none" stroke="#fff" stroke-miterlimit="10" stroke-width="1.15"/><line x1="24.31" y1="24.45" x2="9.31" y2="20.43" fill="none" stroke="#fff" stroke-miterlimit="10" stroke-width="1.15"/><line x1="25.5" y1="20.01" x2="10.5" y2="15.99" fill="none" stroke="#fff" stroke-miterlimit="10" stroke-width="1.15"/></svg>
            </div>
            {/* <!-- Tool 6: Camera Path --> */}
            <div id="tourtool" onClick={toolBtnClick}
            draggable  onDragStart={onDragStart} 
            onDragOver={onDragOver}
            className="tool">
                <svg  xmlns="http://www.w3.org/2000/svg" className="tool-icon" width="36" height="36" viewBox="0 0 36 36"><circle className="tool-icon-bg" cx="18" cy="18" r="18"/><path d="M27.89,11.7a.44.44,0,0,0-.47.06L24.85,14V11.45a.46.46,0,0,0-.46-.45H15.27a.45.45,0,0,0-.45.45v7.22a.45.45,0,0,0,.45.45h9.12a.46.46,0,0,0,.46-.45V16.14l2.57,2.22a.45.45,0,0,0,.47.07.46.46,0,0,0,.25-.4V12.09A.43.43,0,0,0,27.89,11.7Z" fill="#fff"/><path d="M18.68,26.66a1.18,1.18,0,0,0-1,.42,12.14,12.14,0,0,1-9.95-9.95,1.14,1.14,0,0,0,.43-1,1.17,1.17,0,1,0-1.66,1.12,13.38,13.38,0,0,0,11,11,1.16,1.16,0,0,0,1,.68,1.17,1.17,0,1,0,.11-2.34Z" fill="#fff"/></svg>
            </div>
           
        </div>
    </div>
    {/* <!-- /Content --> */}
</div>
);
}

    
        
   

    export default  ToolsDropdown;
    