import React, { Component } from "react";
import Draggable from "react-draggable";
import MessageType from '../../../../models/MessageType';



export default class CaptionPanel extends Component<{ captionPanelCss,getPrintValue }>{
state={
    title:'',
    coordinator:'',
    eventname:'',
    eventdate:'',
    eventnumber:'',
}
onFileUploadClick=()=>{
    if (window.vuplex) {
        window.vuplex.postMessage({ type: MessageType.PRINT, value: JSON.stringify("Upload") });
    }
}
    getValue = (em) => {
        let currentVal = em.target.value;
         let idVal = em.target.id;
        // let jsonArr = this.state.colInspectorValue;
        // let patt = /^[0-9\.\-\/]+$/;
        // if (patt.test(currentVal) || currentVal == '') {
        //     if (currentVal == '-' || jsonArr[idVal] === '.') {
        //         jsonArr[idVal] = currentVal;
        //     }
        //     else if (currentVal != '') {

        //         jsonArr[idVal] = currentVal;
        //     }
        //     else if (currentVal == '') {
        //         jsonArr[idVal] = currentVal;
        //     }

            // else if(jsonArr[idVal]==='-' || jsonArr[idVal]==='.') {
            // 	jsonArr[idVal] = currentVal;
            // }

            this.setState({
                [idVal]: currentVal,            
               });
       // }
        this.props.getPrintValue(this.state.title,this.state.coordinator,this.state.eventname,this.state.eventdate,this.state.eventnumber);


    }

    render() {
        return (
            // <!-- Panel: Captions -->
            <Draggable axis="y" grid={[26, 26]}>
		<div id="captions-panel" className={"captions " + this.props.captionPanelCss}>
				<div className="captions-wrapper">
					<div className="captions-handle grab left">
						{/* <!-- Grab handle --> */}
						<svg xmlns="http://www.w3.org/2000/svg" width="4.9" height="25.3" viewBox="0 0 4.9 25.3"><line y1="24.65" x2="4.9" y2="24.65" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/><line y1="21.65" x2="4.9" y2="21.65" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/><line y1="18.65" x2="4.9" y2="18.65" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/><line y1="15.65" x2="4.9" y2="15.65" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/><line y1="12.65" x2="4.9" y2="12.65" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/><line y1="9.65" x2="4.9" y2="9.65" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/><line y1="6.65" x2="4.9" y2="6.65" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/><line y1="3.65" x2="4.9" y2="3.65" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/><line y1="0.65" x2="4.9" y2="0.65" fill="none" stroke="#cdcdcd" stroke-miterlimit="10" stroke-width="1.3"/></svg>
					</div>
					{/* <!-- Logo Upload --> */}
					<div onClick={this.onFileUploadClick}  className="user-logo left">
						<input  id="logo-upload" type="file"/>
						<label  className="custom-logo-upload pointer">
							<svg xmlns="http://www.w3.org/2000/svg" width="52" height="52" viewBox="0 0 52 52"><circle cx="26" cy="26" r="26" fill="#4d4d4d"/><polyline points="17.81 24.2 26.01 16 34.19 24.17" fill="none" stroke="#fff" stroke-miterlimit="10" stroke-width="3"/><line x1="25.99" y1="36" x2="25.99" y2="16" fill="none" stroke="#fff" stroke-miterlimit="10" stroke-width="3"/></svg>
						</label>
					</div>
					{/* <!-- Title & Coordinator Infomation --> */}
					<div className="cap-block-one left">
						<form id="cap-one">
							<div id="ttl-block">
								<label><h3>Title</h3></label>
								<input type="text" id="title" placeholder="Enter title" value={this.state.title} onChange={this.getValue}/>
							</div>
							<div id="coord-block">
								<label><h3>Coordinator</h3></label>
								<input type="text" id="coordinator" value={this.state.coordinator} onChange={this.getValue} placeholder="Enter coordinator"/>
							</div>
						</form>
					</div>
					{/* <!-- Event Details --> */}
					<div className="cap-block-two left">
						<form id="cap-two">
							<div id="name-block">
								<label className="left" ><h3>Event name</h3></label>
                                <input className="left" type="text" id="eventname"  value={this.state.eventname} onChange={this.getValue}
                                placeholder="Enter event name"/>
							</div>
							<div id="date-block">
								<label className="left" ><h3>Event date</h3></label>
								<input className="left" type="text" id="eventdate" value={this.state.eventdate} onChange={this.getValue} placeholder="Enter event date"/>
							</div>
							<div id="num-block">
								<label className="left" ><h3>Event #</h3></label>
								<input className="left" type="text" id="eventnumber" value={this.state.eventnumber} onChange={this.getValue} placeholder="Enter event #"/>
							</div>
						</form>
					</div>
				</div>
			</div>

            </Draggable>


        );
    }





}