import React, { Component } from 'react';
import MessageType from '../../../models/MessageType';

//const stateSwtichValue=["Print","Showroom Presenter","AR","Spatial Designer"];

// export default class State_Switch extends Component<{ classNameVal?: string }>{
export default class Layout_Settings extends Component {

    state = {

        cssClassForSettingButton: "",
        cssClassForSetttingOpenIcon: "",
        cssClassForSettingCloseIcon: "",
        cssClassForSettingMenuDiv: "",

    };

   btnFeetClick= (event) =>
   {
    window.vuplex.postMessage({ type: MessageType.SETTING_FEET, value: "Feet clicked" });
   }
   btnHideClick= (event) =>
   {
    window.vuplex.postMessage({ type: MessageType.SETTING_HIDE, value: "Hide clicked" });
   }
 
    settingBtnClick = (event) => {
        event.preventDefault();

        if (this.state.cssClassForSettingButton == 'settings-btn-mod') {
            this.setState({
                cssClassForSettingButton: "",
                cssClassForSetttingOpenIcon: "",
                cssClassForSettingCloseIcon: "",
                cssClassForSettingMenuDiv: ""
            });
            if (window.vuplex) {
                window.vuplex.postMessage({ type: MessageType.SETTINGS_OPEN, value: "false" });
                }
        }
        else {
            this.setState({
                cssClassForSettingButton: "settings-btn-mod",
                cssClassForSetttingOpenIcon: "hide",
                cssClassForSettingCloseIcon: "show",
                cssClassForSettingMenuDiv: "show-layoutSettings-content"

            });
            if (window.vuplex) {
                window.vuplex.postMessage({ type: MessageType.SETTINGS_OPEN, value: "true" });
                }
        }

    }


    render() {
        return (
            <div className="layoutSettings dropdown">

                <button onClick={this.settingBtnClick} id="layoutSettings-btn" className={"settings-dropdown-btn " + this.state.cssClassForSettingButton}>
                    <div className="setting-states">
                        <svg xmlns="http://www.w3.org/2000/svg" width="52" height="52" viewBox="0 0 52 52">

                            <g id="opened-state" className={this.state.cssClassForSetttingOpenIcon}>
                                <circle cx="26" cy="26" r="26" fill="#fff" />
                                <path className="btn-icon-fill" d="M38.87,23.41a13.09,13.09,0,0,0-1.94-4.68l1-2-2.65-2.65-2,1a13.09,13.09,0,0,0-4.68-1.94L27.88,11H24.13l-.72,2.13a13.09,13.09,0,0,0-4.68,1.94l-2-1-2.65,2.65,1,2a13.09,13.09,0,0,0-1.94,4.68L11,24.13v3.75l2.13.71a13.09,13.09,0,0,0,1.94,4.68l-1,2,2.65,2.65,2-1a13.09,13.09,0,0,0,4.68,1.94L24.13,41h3.75l.71-2.13a13.09,13.09,0,0,0,4.68-1.94l2,1,2.65-2.65-1-2a13.09,13.09,0,0,0,1.94-4.68L41,27.88V24.13ZM26,36.38A10.38,10.38,0,1,1,36.38,26,10.37,10.37,0,0,1,26,36.38Z" fill="#333" />
                            </g>

                            <g id="closed-state" className={"hide btn-icon-stroke-dark " + this.state.cssClassForSettingCloseIcon}>
                                <line x1="19.25" y1="19.25" x2="32.75" y2="32.75" fill="none" stroke-miterlimit="10" stroke-width="2.5" />
                                <line x1="19.25" y1="32.75" x2="32.75" y2="19.25" fill="none" stroke-miterlimit="10" stroke-width="2.5" />
                            </g>
                        </svg>
                    </div>
                </button>

                <div id="settings" className={"layoutSettings-content " + this.state.cssClassForSettingMenuDiv}>
                    <div id="settingsList" className="settings-wrapper">

                        <div className="setting" onClick={this.btnFeetClick}>
                            <svg xmlns="http://www.w3.org/2000/svg" className="setting-icon" width="36" height="36" viewBox="0 0 36 36">

                                <circle className="setting-icon-bg" cx="18" cy="18" r="18" />

                                <g id="feet">
                                    <path d="M12.45,18H10.27V15.76h2.18v-1.5a12,12,0,0,1,.15-2,4,4,0,0,1,.58-1.53,2.85,2.85,0,0,1,1.2-1,4.93,4.93,0,0,1,2-.34,8.71,8.71,0,0,1,1.67.15l-.17,2.25c-.19-.05-.37-.08-.55-.11a3.31,3.31,0,0,0-.55-.05,1.83,1.83,0,0,0-.88.18,1.48,1.48,0,0,0-.52.47,1.87,1.87,0,0,0-.24.7,5.53,5.53,0,0,0-.05.83v1.92h2.44V18H15.09v8.32H12.45Z" fill="#fff" />
                                    <path d="M18,18V15.76h2.18V12.7h2.64v3.06h2.9V18h-2.9v4.62a2.24,2.24,0,0,0,.3,1.25,1.28,1.28,0,0,0,1.17.47,4.38,4.38,0,0,0,.77-.08,1.69,1.69,0,0,0,.66-.25v2.22a3.44,3.44,0,0,1-1,.27,8.86,8.86,0,0,1-1.07.08,5.46,5.46,0,0,1-1.72-.23,2.3,2.3,0,0,1-1.06-.7,2.72,2.72,0,0,1-.53-1.18,8.69,8.69,0,0,1-.14-1.63V18Z" fill="#fff" />
                                </g>

                                <path id="meters" className="hide" d="M9.82,12.85h2.5V14.5h0a3.52,3.52,0,0,1,1.13-1.33,3.42,3.42,0,0,1,2.1-.58,3.2,3.2,0,0,1,3.3,2,3.74,3.74,0,0,1,1.43-1.49,4.25,4.25,0,0,1,2.05-.47,4.12,4.12,0,0,1,1.78.35,3.12,3.12,0,0,1,1.18,1A4.12,4.12,0,0,1,26,15.35a7.69,7.69,0,0,1,.19,1.77v6.29H23.54v-6a5.77,5.77,0,0,0-.08-.94,2.42,2.42,0,0,0-.31-.79,1.56,1.56,0,0,0-.6-.54,2.07,2.07,0,0,0-1-.2,2.14,2.14,0,0,0-1.05.24,2,2,0,0,0-.7.61,2.44,2.44,0,0,0-.4.88,4.27,4.27,0,0,0-.12,1v5.72H16.68V17.12a2.63,2.63,0,0,0-.42-1.57A1.58,1.58,0,0,0,14.88,15a2.61,2.61,0,0,0-1.11.22,2.12,2.12,0,0,0-.75.6,2.32,2.32,0,0,0-.43.86,3.73,3.73,0,0,0-.13,1v5.76H9.82Z" fill="#fff" />
                            </svg>
                        </div>

                        <div className="setting" onClick={this.btnHideClick}>
                            <svg xmlns="http://www.w3.org/2000/svg" className="setting-icon" width="36" height="36" viewBox="0 0 36 36">
                                <circle className="setting-icon-bg" cx="18" cy="18" r="18" />
                                {
                                    //<!-- Hide Icon -->
                                }

                                <path id="icon-hideObjects" d="M18,13.42a4.17,4.17,0,0,1,4.17,4.16,4,4,0,0,1-.3,1.52l2.44,2.44a10,10,0,0,0,2.86-4A9.88,9.88,0,0,0,18,11.33a9.58,9.58,0,0,0-3.32.59l1.8,1.79A4.24,4.24,0,0,1,18,13.42ZM9.67,11.15l1.89,1.9.38.38a9.88,9.88,0,0,0-3.11,4.15,9.86,9.86,0,0,0,12.82,5.55l.36.35,2.43,2.44,1.06-1.06L10.73,10.08Zm4.6,4.6L15.56,17a2.5,2.5,0,0,0,2.44,3,2.42,2.42,0,0,0,.54-.06l1.29,1.29a4.14,4.14,0,0,1-5.56-5.56Zm3.59-.65,2.63,2.62v-.14a2.5,2.5,0,0,0-2.5-2.5Z" fill="#fff" />
                                {
                                    //<!-- Show Icon -->
                                }

                                <path id="icon-showObjects" className="hide" d="M18,11.75A9.85,9.85,0,0,0,8.83,18a9.85,9.85,0,0,0,18.34,0A9.85,9.85,0,0,0,18,11.75Zm0,10.42A4.17,4.17,0,1,1,22.17,18,4.17,4.17,0,0,1,18,22.17Zm0-6.67A2.5,2.5,0,1,0,20.5,18,2.5,2.5,0,0,0,18,15.5Z" fill="#fff" />
                            </svg>
                        </div>

                    </div>
                    {
                        // <!-- /settings-wrapper -->
                    }
                </div>
                {
                    //  <!-- layoutSettings-content -->
                    //   <!-- /Content -->
                }
            </div>

        );
    }
}