import React,{ Component } from "react";


export default class PrintArea extends Component{

render()
{
    return(

        // <!-- Print Area -->
        <div className="print-area">
            <div className="print-area-divider left"><hr/></div>
            <div className="print-area-text left"><h3>print area above</h3></div>
            <div className="print-area-divider right"><hr/></div>
        </div>

    );
}


}