import React,{ Component } from "react";
import MessageType from "../../../../models/MessageType";



export default class PrintBtn extends Component<{title,coordinator,eventname,eventdate,eventnumber}>{

  onPrintClick=()=>
  {

let jsonVal='{"title":"'+this.props.title+'","coordinator":"'+this.props.coordinator+
             '","eventname":"'+this.props.eventname+'","eventdate":"'+this.props.eventdate+'","eventnumber":"'+this.props.eventnumber+'"}';
 
  console.log(jsonVal);      

             if (window.vuplex) {
              window.vuplex.postMessage({ type: MessageType.PRINT, value: jsonVal });
          }

  }

render()
{
  return(
    // <!-- Button: Print -->
    <div onClick={this.onPrintClick} className="print">
        <svg xmlns="http://www.w3.org/2000/svg" width="52" height="52" viewBox="0 0 52 52">
            <rect className="btn-bg" width="52" height="52" rx="26"/>
            <g className="btn-icon-stroke">
                <rect x="12.12" y="19.72" width="27.76" height="15.19" rx="2.36" fill="none"
                 stroke-miterlimit="10" stroke-width="2.09"/>
                <rect x="18.14" y="11.86" width="15.71" height="7.86" rx="1.05" fill="none" 
                stroke-miterlimit="10" stroke-width="2.09"/>
                <rect x="18.14" y="29.67" width="15.71" height="10.47" rx="1.21" fill="none"
                 stroke-miterlimit="10" stroke-width="2.09"/>
            </g>
            <circle className="btn-icon-fill" cx="16.85" cy="24.7" r="1.31" fill="#333"/>
        </svg>
    </div>
    // <!-- Shadow: Print Button -->

  );
}


}