import React, { Component } from 'react';
//import MessageType from '../../../models/MessageType';

//const stateSwtichValue=["Print","Showroom Presenter","AR","Spatial Designer"];
//import axios from 'axios';
// export default class State_Switch extends Component<{ classNameVal?: string }>{
export default class User_Tag extends Component<{ userEmail, userList }>{
  _isMounted = false;
  state = {


    user_layout_cssForButton: "",
    user_layout_cssForMenu: "",
    user_layout_arrow: "",
    loggedUserEmail: '',
    userTagValue: [],
    panelColorName: ["FFE765",
      "79DBF8",
      "8F7AD8",
      "FF6F80",
      "8CEAA7",
      "EFB16F",
      "EF84DB",
      "7186FF",
      "C3D670",
      "757575",
    ]
  };
  userlayoutBtnClick = (event) => {
    event.preventDefault();
    if (this.state.userTagValue.length != 0) {
      if (this.state.user_layout_cssForButton == 'userTag-btn-mod') {
        this.setState({
          user_layout_cssForButton: "",
          user_layout_cssForMenu: "",
          user_layout_arrow: "",
        });
      }
      else {
        this.setState({
          user_layout_cssForButton: "userTag-btn-mod",
          user_layout_cssForMenu: "show-userTag-content",
          user_layout_arrow: "arrow-rotate",

        });
      }
    }
  }


  constructor(props) {
    super(props);

  }
  componentDidMount() {
    this._isMounted = true;

  }
  static getDerivedStateFromProps(props, state) {
    let loggedUserEmail = '';
    let invitedUser = '';
    let userLists = [] as any;

    let res;
    // if (window.vuplex) {
    //   window.vuplex.postMessage({ type: MessageType.test_inspector_data, value: JSON.stringify(props.userList["action"]) });
    // }
    if (props.userEmail != '' && props.userEmail != undefined) {
      loggedUserEmail = props.userEmail;
      res = loggedUserEmail.split('@');
      loggedUserEmail = res[0] + '@...';

    }
    if (props.userList["userName"] != '') {

      let userList = {
        userEmail: "",
        panelColor: "",

      };

      if (props.userList["action"].toLowerCase() == "join") {
        userLists = state.userTagValue;
        invitedUser = props.userList["userName"];
        res = invitedUser.split('@');
        invitedUser = res[0] + '@...';
        userList.userEmail = invitedUser;
        let indexValue = userLists.findIndex(function (data) {

          return data["userEmail"] == invitedUser;
        });

        if (indexValue == -1) {


          let totalCnt = state.panelColorName.length;
          let randomIndex = Math.floor(Math.random() * totalCnt) + 1;
          userList.panelColor = state.panelColorName[randomIndex];

          userLists.push(userList);
          return {
            userTagValue: userLists,
          };
        }
      }
      else {
        let indexValue = {};
        indexValue = state.userTagValue.findIndex(function (data) {

          return data["userEmail"] == props.userList["userName"];
        });
        state.userTagValue.splice(indexValue, 1);
        return {
          state,
        }
      }

    }
    return {
      loggedUserEmail: loggedUserEmail,

    };


  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  userTagValues = ({ userTagValue }) => {

    return (


      userTagValue.map((userTagData, key) => (
        <div key={key} className="userName" style={{ background: "#" + userTagData.panelColor }}><p>
          {/* <span><svg xmlns="http://www.w3.org/2000/svg"
      className="userName-icon left" width="36" height="36" viewBox="0 0 36 36">
        <circle cx="18" cy="18" r="18" fill="none" />
      <circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span> */}
          {userTagData.userEmail}</p>
        </div>
      ))

    );


  }

  createUserTag() {
    ///this.setColorTousers(this.i);
    //this.i++;
    return (

      <this.userTagValues userTagValue={this.state.userTagValue} />

    );
  }

  render() {
    return (
      <div className="userTag dropdown">

        <button id="userTag-btn" className={"user-dropdown-btn " + this.state.user_layout_cssForButton}
          onClick={this.userlayoutBtnClick}>
          <div className="yourUserName blue">

            {/* <span className="left"><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="5.24" fill="#333" /><path d="M12.14,24.93a9,9,0,0,1,0-13.9" fill="none" stroke="#333" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2.04" /><path d="M7.75,27.75a13,13,0,0,1,0-19.5" fill="none" stroke="#333" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2.04" /><path d="M23.86,11.07a9,9,0,0,1,0,13.9" fill="none" stroke="#333" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2.04" /><path d="M28.25,8.25a13,13,0,0,1,.05,19.5" fill="none" stroke="#333" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2.04" /></svg></span> */}

            <p className="left">{this.state.loggedUserEmail == "" ? this.props.userEmail : this.state.loggedUserEmail}</p>

            {/* <span className="right"><svg xmlns="http://www.w3.org/2000/svg" width="36" 
            height="36" viewBox="0 0 36 36"><path id="userTag-arrow"
             className={"arrow "+this.state.user_layout_arrow} d="M12.15,13.18h11.7A1.5,1.5,0,0,1,25,15.67l-5.84,6.64a1.51,1.51,0,0,1-2.26,0L11,15.67A1.5,1.5,0,0,1,12.15,13.18Z" fill="#333"
              /></svg></span> */}

            <p id="userCount" className="right">{this.state.userTagValue.length == 0 ? null : "+" + this.state.userTagValue.length}</p>
          </div>
        </button>


        {this.state.userTagValue.length != 0 ?
          <div id="users" className={"userTag-content " + this.state.user_layout_cssForMenu}>
            <div id="userList" className="user-wrapper">
              {this.createUserTag()}

              {/* <div className="userName purple"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span>Team Member Name</p></div>

            <div className="userName green"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="5.24" fill="#333" /><path d="M12.14,24.93a9,9,0,0,1,0-13.9" fill="none" stroke="#333" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2.04" /><path d="M7.75,27.75a13,13,0,0,1,0-19.5" fill="none" stroke="#333" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2.04" /><path d="M23.86,11.07a9,9,0,0,1,0,13.9" fill="none" stroke="#333" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2.04" /><path d="M28.25,8.25a13,13,0,0,1,.05,19.5" fill="none" stroke="#333" stroke-linecap="round" stroke-miterlimit="10" stroke-width="2.04" /></svg></span>Team Member Name</p></div>

            <div className="userName yellow-green"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span>Team Member Name</p></div>

            <div className="userName orange"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span>Team Member Name</p></div>

            <div className="userName red"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span>Team Member Name</p></div>

            <div className="userName pink"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span>Team Member Name</p></div>

            <div className="userName purple"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span>Team Member Name</p></div>

            <div className="userName green"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span>Team Member Name</p></div>

            <div className="userName yellow-green"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span>Team Member Name</p></div>

            <div className="userName orange"><p><span><svg xmlns="http://www.w3.org/2000/svg" className="userName-icon left" width="36" height="36" viewBox="0 0 36 36"><circle cx="18" cy="18" r="18" fill="none" /><circle cx="18" cy="18" r="5.24" fill="#333" /></svg></span>Team Member Name</p></div> */}
            </div>
          </div>
          : null}
      </div>
    );
  }
}