import React,{ Component } from 'react';
import axios from 'axios';
import MessageType from '../../../models/MessageType';
import Keyboard from '../../Keyboard';
import '../styles.scss';
import LoginUi from './loginUI';
import config from '../config';
//import { config } from 'process';


export default class Login extends Component {
	_isMounted = false;
	state = {

		loginFlag: false,
		loginData: [],
		loginHttpCode: "404",
		username: "",
		firstname:"",
		email:"",
		userId:"",
		password: "",
		errorMsg: "",
		cssStyle: "",
		stateData:[],
		
	};
	constructor(props) {
		super(props);
		//this.setState({flag:true})

	}
	componentDidMount() {

	}
	onChangeSetValue = (event) => {

		this.setState({
			[event.target.name]: event.target.value,

		});
	}
	onLoginClick = (event) => {
		event.preventDefault();
		this._isMounted = true;

          console.log(config.env);
		
		 // const url = "http://freeman-api-dev.geogram.co/userlogin/";
           let url ="";
		 if(config.env ==="dev")
		 {
               url = config.devApi+config.devLoginUrl;
		 }
		 else if(config.env === "prod")
		 {
			url = config.prodApi+config.devLoginUrl;
		 }

		//console.log(this.state.username + "-- " + this.state.password);

		// const param = JSON.stringify({
		// 	"username":this.state.username,
		// 	"pwd":this.state.password
		// });
		const param = {
			"username":this.state.username,
			"pwd":this.state.password
		};
		axios.post(url, param, {
			headers: [

				{ "Access-Control-Allow-Origin": '*' },
				{ "Access-Control-Allow-Headers": 'Origin, X-Requested-With, Content-Type:application/json, Accept' },
				{ "Access-Control-Allow-Methods": "POST, GET, PUT, OPTIONS, DELETE" },
				{ "Access-Control-Max-Age": 3600 }
			],

		})	
			.then(res => {

				// console.log(res);
				 console.log(res.data);

			
				if (this._isMounted) {
					if (res.data.code == 200) {

						this.setState({
							loginFlag: true,
							errorMsg: "",
							cssStyle: "",
							loginHttpCode: res.data.code,
							loginData: [...this.state.loginData, res.data.userdet.stateMode],
							stateData:res.data.userdet.stateMode,
							userId:res.data.userdet._id,
							email:res.data.userdet.email,
							firstname:res.data.userdet.firstName,
						});
						//passing repsonse to unity
						if (window.vuplex) {
							window.vuplex.postMessage({ type: MessageType.LOGIN_RESPONSE, value: JSON.stringify(res.data) });
						}
						// else{
						// 	console.log(JSON.stringify(res.data));
						// }
						
						// console.log(this.state.stateData);
					}
					else {
						this.setState({
							errorMsg: "USERNAME DOESN'T EXISTS. PLEASE TRY AGAIN",
							cssStyle: "red",
						});
					}
					// this.setState({
					// 	loginHttpCode: res.data.code
					// });

					// // for (let i = 0; i < res.data.userdet.length; i++) {

					// this.setState({
					// 	loginData: [...this.state.loginData, res.data.userdet]
					// });

					// }
				}
				//	this.setState({ projectValue:[...this.state.projectValue,res.data.projects]}) ;
				//  console.log(this.state.loginHttpCode);
				//console.log(this.state.loginData);
				//console.log(this.state.loginData[0]["firstName"]);
				//console.log(this.state.loginFlag);
			});

		// this._isMounted=false;



	}
	componentWillUnmount() {
		this._isMounted = false;
	}
	render() {

		return (
			this.state.loginFlag ? <Keyboard loggedUserEmail={this.state.email} userId={this.state.userId} 
			loggedUserData={this.state.loginData} stateData={this.state.stateData} userName={this.state.firstname} /> :
				<div>

					{/* <div className="sidebars"></div> */}
					<LoginUi onFormDataSubmit={this.onLoginClick} onInputChange={this.onChangeSetValue}
						errMsg={this.state.errorMsg} errMsgStyle={this.state.cssStyle} />
				</div>

		);

	}



}