import React, { Component } from 'react';
import MessageType from '../../../../models/MessageType';
import axios from 'axios';
import config from '../../config';
let mediaData = [];
export default class MediaInspector extends Component<{ flag, inspectorValue }>{

	state = {
		mediaLockCss:'hide',
		mediaUnLockCss:'show',
		mediaInspectorValue: {
			mPosX: '*',
			mPosY: '*',
			mPosZ: '*',
			mRotX: '*',
			mRotY: '*',
			mRotZ: '*',
			mScaleX: '*',
			mScaleY: '*',
			mScaleZ: '*'
		},
		mediaInspectorEditStatus: false,
		locked: false,
		Hidden: false,
		objectName: '',
		mediaData: [],
		mediaVisible: 'show',
		mediaUrl: '/static/media/image.1d9a793c.png',
		mediaSelectedId:'',
        mediaValue:'',
	}
	componentDidMount() {
		this.getMediaList();
	}
	static getDerivedStateFromProps(props, state) {
		let lockStatus = props.inspectorValue.locked;




		if (lockStatus != undefined) {

			let arr = [] as any;
			let jsonVal = props.inspectorValue;


			//console.log(jsonVal);

			Object.keys(jsonVal).forEach(function (key) {


				if (key === 'position' || key === 'rotation' || key === 'scale')
					arr.push(key);

			});

			arr.forEach(element => {
				Object.keys(jsonVal[element]).forEach(function (key) {

					if (jsonVal[element][key] != Math.floor(jsonVal[element][key])) {
						let num = jsonVal[element][key].toFixed(2);
						jsonVal[element][key] = parseFloat(num);

					}
				});


			});
			let _mediaLockCss =state.mediaLockCss;
			let _mediaUnLockCss =state.mediaUnLockCss;
			let locked=state.locked;
			if(jsonVal.locked==true)
			{
			 _mediaLockCss ="show";
			 _mediaUnLockCss ="hide";
			 locked =true;
			}
			let Hidden= state.Hidden;
			if(jsonVal.hidden==true)
			{
				Hidden=true;
			}
			//--------------------------------------------------------------------  
			if (state.mediaLockCss == '' && state.mediaUnLockCss == '') {
				if (lockStatus == true && state.mediaInspectorEditStatus == false) {

					return {
						mediaLockCss: "show",
						mediaUnLockCss: "hide",
						mediaInspectorValue:
						{
							mPosX: jsonVal.position.x,
							mPosY: jsonVal.position.y,
							mPosZ: jsonVal.position.z,
							mRotX: jsonVal.rotation.x,
							mRotY: jsonVal.rotation.y,
							mRotZ: jsonVal.rotation.z,
							mScaleX: jsonVal.scale.x,
							mScaleY: jsonVal.scale.y,
							mScaleZ: jsonVal.scale.z,
						},


						objectName: jsonVal.objectName,

					};
				}
				if (lockStatus == true && state.mediaInspectorEditStatus == true) {

					return {
						mediaInspectorEditStatus: false,

						state


					};
				}

				if (lockStatus == false && state.mediaInspectorEditStatus == false) {
					return {
						mediaLockCss: "hide",
						mediaUnLockCss: "show",
						mediaInspectorValue:
						{
							mPosX: jsonVal.position.x,
							mPosY: jsonVal.position.y,
							mPosZ: jsonVal.position.z,
							mRotX: jsonVal.rotation.x,
							mRotY: jsonVal.rotation.y,
							mRotZ: jsonVal.rotation.z,
							mScaleX: jsonVal.scale.x,
							mScaleY: jsonVal.scale.y,
							mScaleZ: jsonVal.scale.z,
						},


						objectName: jsonVal.objectName,
					};
				}
				if (lockStatus == false && state.mediaInspectorEditStatus == true) {
					return {
						mediaInspectorEditStatus: false,
						state

					};

				}


			}

			if (state.mediaInspectorEditStatus == false) {
				return {

					mediaInspectorValue:
					{
						mPosX: jsonVal.position.x,
						mPosY: jsonVal.position.y,
						mPosZ: jsonVal.position.z,
						mRotX: jsonVal.rotation.x,
						mRotY: jsonVal.rotation.y,
						mRotZ: jsonVal.rotation.z,
						mScaleX: jsonVal.scale.x,
						mScaleY: jsonVal.scale.y,
						mScaleZ: jsonVal.scale.z,
					},
					mediaLockCss: _mediaLockCss,
					mediaUnLockCss:_mediaUnLockCss,
					//inspectorLoadForFirst:inspectorLoadForFirst,
					objectName: jsonVal.objectName,
					locked:locked,
					Hidden:Hidden,
					//lightClicked:lightClicked,
				};
			}
			if (state.mediaInspectorEditStatus == true) {
				return {
					mediaInspectorEditStatus: false,
					state

				};
			}




		}

		return null;
		// basicLockStatus: lockStatus,  	  

	}
	getJsonFormat(jsonValue) {
		let jsonArr = jsonValue;
		//console.log(e.target.value);
		let arr = [] as any;
		//console.log(jsonVal);

		Object.keys(jsonArr).forEach(function (key) {

			arr.push(key);
		});
		let patt = /-?\d*\.{0,1}\d+$/;
		arr.forEach(element => {
			// if (jsonArr[element] == '') {
			// 	jsonArr[element] = 0;
			// }
			// else 
			if (patt.test(jsonArr[element])) {
				jsonArr[element] = parseFloat(jsonArr[element]);
			}
			else {
				jsonArr[element] = 0;
			}

		});

		//	console.log(jsonVal);


		this.setState({
			mediaInspectorValue: jsonArr,
		});
	}

	getMediaList() {
		let url = "";
		if (config.env = "dev") {
			url = config.devApi + config.devMediaUrl;
		}
		else if (config.env = "prod") {
			url = config.prodApiApi + config.proddMediaUrl;
		}



		axios.get(url, {
			headers: [

				{ "Access-Control-Allow-Origin": '*' },
				{ "Access-Control-Allow-Headers": 'Origin, X-Requested-With, Content-Type, Accept ' },
				{ "Access-Control-Allow-Methods": "POST, GET, PUT, OPTIONS, DELETE" },
				{ "Access-Control-Max-Age": 3600 }
			]
		})
			.then(res => {

				// console.log(res.data);
				let tempArr = [{}] as any;
				tempArr.pop();
				for (let i = 0; i < res.data.length; i++) {
					if (res.data[i]["mediaType"] === "image" || res.data[i]["mediaType"] === "video") {
						tempArr.push(res.data[i]);
					}
				}
				//  console.log(tempArr);
				this.setState({ mediaData: tempArr });
				console.log(this.state.mediaData);


				//	console.log(res.data);
				// if (window.vuplex) {
				// 	window.vuplex.postMessage({ type: MessageType.LAYOUT_RESPONSE, value: JSON.stringify(res.data) });
				// }







			});

	}
	hoverDisplayDetails = (e) => {
		let idVal = e.target.id;

		if (mediaData.length == 0) {
			mediaData = this.state.mediaData;
		}

		let jsonVal = {};
		jsonVal = mediaData.filter(function (data) {

			return data["_id"] == idVal;
		});

		console.log(jsonVal[0]["mediaLink"]);

		this.setState({
			mediaVisible: 'show',
			mediaUrl: jsonVal[0]["mediaLink"],
		});






	}
	mediaItemClickHandler = (e) => {
		let idVal = e.target.id;
		this.setState({mediaSelectedId:idVal});
		let jsoninspectorValue = {
			"position": {
				"x": this.state.mediaInspectorValue.mPosX,
				"y": this.state.mediaInspectorValue.mPosY, "z": this.state.mediaInspectorValue.mPosZ
			},
			"rotation": {
				"x": this.state.mediaInspectorValue.mRotX,
				"y": this.state.mediaInspectorValue.mRotY,
				"z": this.state.mediaInspectorValue.mRotZ
			},
			"scale": {
				"x": this.state.mediaInspectorValue.mScaleX,
				"y": this.state.mediaInspectorValue.mScaleY,
				"z": this.state.mediaInspectorValue.mScaleZ
			},
			"locked": this.state.locked, "Hidden": this.state.Hidden,
		   "mediavalue":this.state.mediaValue,
		};

		
	
		if (mediaData.length == 0) {
			mediaData = this.state.mediaData;
		}
		let jsonVal = {};
		jsonVal = mediaData.filter(function (data) {

			return data["_id"] == idVal;
		});
		this.setState({
			mediaValue:JSON.stringify(jsonVal[0]),
		});
           jsoninspectorValue["mediavalue"]=JSON.stringify(jsonVal[0]);

		   console.log(jsoninspectorValue);
		if (window.vuplex) {
			window.vuplex.postMessage({ type: MessageType.MEDIA_TOOL_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
		}

		//    console.log(this.state.mediacheck);
		//    <this.renderMedia mediaValue={this.state.mediaData} />
		//    break;
		// }
      
		

	}
	renderMedia = ({ mediaValue }) => {

		return (
			mediaValue.map((mediaData, key) => (
				<div>
					<div id={mediaData._id} onMouseOver={this.state.locked?undefined:(e) => this.hoverDisplayDetails(e)}
						onClick={this.state.locked?undefined:(e) => this.mediaItemClickHandler(e)} className="mediaName">
						<p id={mediaData._id} className="left" onClick={this.state.locked?undefined:(e) => this.mediaItemClickHandler(e)} >
							{mediaData.mediaName}
						</p>
						{this.state.mediaSelectedId ==mediaData._id?
						<span id={mediaData._id}  className="right check show">
						<svg id={mediaData._id} xmlns="http://www.w3.org/2000/svg" width="16.11" height="11.42" viewBox="0 0 16.11 11.42">
							<polyline id={mediaData._id} points="1.5 5.23 6.19 9.92 14.61 1.5" fill="none" stroke="#fff"
								stroke-linecap="round" stroke-linejoin="round" stroke-width="3" /></svg>
						</span>
						:
						<span id={mediaData._id}  className="right check hide">
			<svg   id={mediaData._id} xmlns="http://www.w3.org/2000/svg" width="16.11" height="11.42" viewBox="0 0 16.11 11.42">
				<polyline id={mediaData._id} points="1.5 5.23 6.19 9.92 14.61 1.5" fill="none" stroke="#fff"
					stroke-linecap="round" stroke-linejoin="round" stroke-width="3" /></svg>
		    </span>
						}
						
					</div>
					<hr />
				</div>

			))

		);


	}

	handleKeyDown = (e) => {

		// let currentVal = e.target.value;
		// let idVal = e.target.id;
		if (e.key === 'Enter') {



			this.setState({
				mediaInspectorEditStatus: true
			});


			this.getJsonFormat(this.state.mediaInspectorValue);


			let jsoninspectorValue = {
				"position": {
					"x": this.state.mediaInspectorValue.mPosX,
					"y": this.state.mediaInspectorValue.mPosY, "z": this.state.mediaInspectorValue.mPosZ
				},
				"rotation": {
					"x": this.state.mediaInspectorValue.mRotX,
					"y": this.state.mediaInspectorValue.mRotY,
					"z": this.state.mediaInspectorValue.mRotZ
				},
				"scale": {
					"x": this.state.mediaInspectorValue.mScaleX,
					"y": this.state.mediaInspectorValue.mScaleY,
					"z": this.state.mediaInspectorValue.mScaleZ
				},
				"locked": this.state.locked, "Hidden": this.state.Hidden,
				"mediavalue":this.state.mediaValue,
			};


			console.log(jsoninspectorValue);


			if (window.vuplex) {
				window.vuplex.postMessage({ type: MessageType.MEDIA_TOOL_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
			}
		}//end of enter key code






	}
	mediaLockToggle = (event) => {


		if (this.state.mediaInspectorEditStatus == true) {
			this.getJsonFormat(this.state.mediaInspectorValue);
		}
		this.setState({ mediaInspectorEditStatus: true });
		let locked = false;
		let jsoninspectorValue = {
			"position":
			{
				"x": this.state.mediaInspectorValue.mPosX != '*' ? this.state.mediaInspectorValue.mPosX : this.props.inspectorValue.position.x,

				"y": this.state.mediaInspectorValue.mPosY != '*' ? this.state.mediaInspectorValue.mPosY : this.props.inspectorValue.position.y,

				"z": this.state.mediaInspectorValue.mPosZ != '*' ? this.state.mediaInspectorValue.mPosZ : this.props.inspectorValue.position.z
			},

			"rotation": {
				"x": this.state.mediaInspectorValue.mRotX != '*' ? this.state.mediaInspectorValue.mRotX : this.props.inspectorValue.rotation.x,

				"y": this.state.mediaInspectorValue.mRotY != '*' ? this.state.mediaInspectorValue.mRotY : this.props.inspectorValue.rotation.y,

				"z": this.state.mediaInspectorValue.mRotZ != '*' ? this.state.mediaInspectorValue.mRotZ : this.props.inspectorValue.rotation.z
			},

			"scale": {
				"x": this.state.mediaInspectorValue.mScaleX != '*' ? this.state.mediaInspectorValue.mScaleX : this.props.inspectorValue.scale.x,

				"y": this.state.mediaInspectorValue.mScaleY != '*' ? this.state.mediaInspectorValue.mScaleY : this.props.inspectorValue.scale.y,

				"z": this.state.mediaInspectorValue.mScaleZ != '*' ? this.state.mediaInspectorValue.mScaleZ : this.props.inspectorValue.scale.z
			},

			"locked": locked, "Hidden": this.state.Hidden,
			"mediavalue":this.state.mediaValue,


		};

		if (this.state.mediaLockCss === "hide") {
			locked = true;
			this.setState({
				mediaLockCss: "show",
				mediaUnLockCss: "hide",
				locked: locked,
			});
			//lock status  value updated in Unity json...
			jsoninspectorValue["locked"] = locked;



		//	console.log(jsoninspectorValue);


			if (window.vuplex) {
				window.vuplex.postMessage({ type: MessageType.MEDIA_TOOL_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
			}
		}

		else {
			locked = false;
			this.setState({
				mediaLockCss: "hide",
				mediaUnLockCss: "show",
				locked: locked,
			});
			//lock status  value updated in Unity json...
			jsoninspectorValue["locked"] = locked;

			console.log(jsoninspectorValue);


			if (window.vuplex) {
				window.vuplex.postMessage({ type: MessageType.MEDIA_TOOL_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
			}

		}

	}
	hiddenToggle = (event) =>{
		let Hidden = false;
		if(this.state.mediaInspectorEditStatus == true )
		{
			this.getJsonFormat(this.state.mediaInspectorValue);
        }
        this.setState({mediaInspectorEditStatus:true,});
		let jsoninspectorValue = {
			"position":
			{
				"x": this.state.mediaInspectorValue.mPosX != '*' ? this.state.mediaInspectorValue.mPosX : this.props.inspectorValue.position.x,

				"y": this.state.mediaInspectorValue.mPosY != '*' ? this.state.mediaInspectorValue.mPosY : this.props.inspectorValue.position.y,

				"z": this.state.mediaInspectorValue.mPosZ != '*' ? this.state.mediaInspectorValue.mPosZ : this.props.inspectorValue.position.z
			},

			"rotation": {
				"x": this.state.mediaInspectorValue.mRotX != '*' ? this.state.mediaInspectorValue.mRotX : this.props.inspectorValue.rotation.x,

				"y": this.state.mediaInspectorValue.mRotY != '*' ? this.state.mediaInspectorValue.mRotY : this.props.inspectorValue.rotation.y,

				"z": this.state.mediaInspectorValue.mRotZ != '*' ? this.state.mediaInspectorValue.mRotZ : this.props.inspectorValue.rotation.z
			},

			"scale": {
				"x": this.state.mediaInspectorValue.mScaleX != '*' ? this.state.mediaInspectorValue.mScaleX : this.props.inspectorValue.scale.x,

				"y": this.state.mediaInspectorValue.mScaleY != '*' ? this.state.mediaInspectorValue.mScaleY : this.props.inspectorValue.scale.y,

				"z": this.state.mediaInspectorValue.mScaleZ != '*' ? this.state.mediaInspectorValue.mScaleZ : this.props.inspectorValue.scale.z
			},

			"locked": this.state.locked, "Hidden": this.state.Hidden,
			"mediavalue":this.state.mediaValue,


		};
		if(this.state.Hidden==true)
		{
			Hidden = false;
			this.setState({
				
				Hidden:Hidden,
			});

		    jsoninspectorValue["Hidden"] = Hidden;
		
            if (window.vuplex) {
                window.vuplex.postMessage({ type: MessageType.VOLUMETRIC_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
            }
            console.log(jsoninspectorValue);
		}
		else
		{
			Hidden =true;
			this.setState({
				
				Hidden:Hidden,
			});
            jsoninspectorValue["Hidden"] = Hidden;
			
            console.log(jsoninspectorValue);
            if (window.vuplex) {
                window.vuplex.postMessage({ type: MessageType.VOLUMETRIC_INSPECTOR, value: JSON.stringify(jsoninspectorValue) });
            }
		}





	}
	// mediaLockToggle = ()=> {
	// 	if(this.state.mediaLockCss ==="show")
	//     {
	//         this.setState({
	//             mediaLockCss: 'hide',
	//             mediaUnLockCss: 'show',
	//         });
	//     }
	//     else{
	//         this.setState({
	//             mediaLockCss: 'show',
	//             mediaUnLockCss: 'hide',
	//         });
	//     }


	// }
	getValue = (em) => {
		let currentVal = em.target.value;
		let idVal = em.target.id;
		let jsonArr = this.state.mediaInspectorValue;
		let patt = /^[0-9\.\-\/]+$/;
		if (patt.test(currentVal) || currentVal == '') {
			if (currentVal == '-' || jsonArr[idVal] === '.') {
				jsonArr[idVal] = currentVal;
			}
			else if (currentVal != '') {

				jsonArr[idVal] = currentVal;
			}
			else if (currentVal == '') {
				jsonArr[idVal] = currentVal;
			}

			// else if(jsonArr[idVal]==='-' || jsonArr[idVal]==='.') {
			// 	jsonArr[idVal] = currentVal;
			// }

			this.setState({
				mediaInspectorValue: jsonArr,
				mediaInspectorEditStatus: true,


			});
		}



	}
	render() {
		return (



			<div className="media-inspector">
				<div id="media" className={"media-wrapper " + this.props.flag}>
					{/* <!-- Media Object Details --> */}
					<div className="mediaDetails-wrapper">
						{/* <!-- To add thumbnails, <div> must have class "media-thumbnail" -->
						<!-- Placeholder Media 1 Details --> */}
						<div className={"media-thumbnail " + this.state.mediaVisible} style={{ backgroundImage: 'url(' + this.state.mediaUrl + ')' }}></div>
						{/* <!-- Placeholder Media 2 Details --> */}

					</div>
					{/* <!-- Media List --> */}
					<div className="media-outerContainer">
						<div className="media-innerContainer">
							{this.state.mediaData != null ?
								<this.renderMedia mediaValue={this.state.mediaData} />

								: null}

						</div>
					</div>
					<hr className="media-rowTop" />
					{/* <!-- Asset Name & Icons --> */}
					<div className="media-row-one">
						{/* <!-- Asset Name --> */}
						<h1 id="mediaName-text" className="left">{this.state.objectName != '' ? this.state.objectName : 'Video Media Object'}</h1>
						{/* <!-- Show/Hide --> */}
						<span className="right pointer" onClick={this.state.locked?undefined:this.hiddenToggle}><svg xmlns="http://www.w3.org/2000/svg"
							width="36" height="36" viewBox="0 0 36 36"><defs>
							</defs><path className="a" d="M7.22,10.74a12.92,12.92,0,0,0-2,4.95L11.06,7A12.82,12.82,0,0,0,7.22,10.74Z" /><path className="a" d="M28.77,25.28a13,13,0,0,0,2-4.84L25,28.94A13,13,0,0,0,28.77,25.28Z" /><path className="a" d="M29,11.17,15.79,30.81a13.07,13.07,0,0,0,3.64.11L30.5,14.5A12.5,12.5,0,0,0,29,11.17Z" /><path className="a" d="M25.26,7.23a12.15,12.15,0,0,0-1.09-.66L9.7,28c.33.27.67.52,1,.76a11.57,11.57,0,0,0,1.56.89L26.68,8.35A13,13,0,0,0,25.26,7.23Z" /><path className="a" d="M16.93,5.06,5.59,21.87a12.84,12.84,0,0,0,1.52,3.25L20.51,5.25A13.06,13.06,0,0,0,16.93,5.06Z" /></svg></span>
						{/* <!-- Lock/Unlock Icon --> */}
						<span className="right pointer" onClick={this.mediaLockToggle} >
							<svg xmlns="http://www.w3.org/2000/svg" width="36" height="36"
								viewBox="0 0 36 36">
								{/* <!-- Locked --> */}
								<path id="media-lock" className={"btn-icon-fill-dark " + this.state.mediaLockCss}
									d="M13.63,16.16V14.29a4.2,4.2,0,0,1,1.95-3.65,4.14,4.14,0,0,1,2.92-.7,4.25,4.25,0,0,1,3.41,2.37,4,4,0,0,1,.46,1.86v2h.89c.37,0,.5.14.5.5v8.93c0,.37-.13.51-.5.51H12.74c-.36,0-.5-.14-.5-.51V16.66c0-.36.14-.5.5-.5Zm1.79,0h5.16V14.09a1.07,1.07,0,0,0,0-.25,2.58,2.58,0,0,0-5.13.41v1.9Z" />
								{/* <!-- Unlocked --> */}
								<path id="media-unlock" className={"btn-icon-fill-dark " + this.state.mediaUnLockCss} d="M24.33,19.1l-.63-.62.11-.12c.43-.43.87-.86,1.3-1.3a3.92,3.92,0,0,0,1-1.63,4.26,4.26,0,0,0-.74-4.09,4.13,4.13,0,0,0-2.55-1.57,4.28,4.28,0,0,0-2.69.3l1.43,1.43a2.58,2.58,0,0,1,2.5,4.05.7.7,0,0,1-.15.19l-.06.06h0l-.76.77-.64.63h0v0l-5.59-5.59c-.23-.21-.42-.2-.66,0L9.87,18c-.26.26-.26.46,0,.71l7.43,7.44c.26.26.46.26.71,0l6.32-6.32A.42.42,0,0,0,24.33,19.1Z" />
							</svg>
						</span>
					</div>
					{/* <!-- Position --> */}
					<div className="media-row-half">
						<div className="left media-cell-one pad-right-half"><h3>POS</h3></div>
						<div className="left pad-right-half"><input id="mPosX" type="number" placeholder="X"  disabled = {this.state.locked?true:false}  onKeyDown={this.handleKeyDown} value={this.state.mediaInspectorValue.mPosX} onChange={this.getValue} step=".01" title="X" /></div>
						<div className="left pad-right-half"><input id="mPosY" type="number" placeholder="Y"  disabled = {this.state.locked?true:false}  step=".01" onKeyDown={this.handleKeyDown} value={this.state.mediaInspectorValue.mPosY} onChange={this.getValue} title="Y" /></div>
						<div className="left"><input id="mPosZ" type="number" placeholder="Z"  disabled = {this.state.locked?true:false}  step=".01" onKeyDown={this.handleKeyDown} value={this.state.mediaInspectorValue.mPosZ} onChange={this.getValue} title="Z" /></div>
					</div>
					{/* <!-- Rotation --> */}

					<div className="media-row-half">
						<div className="left media-cell-one pad-right-half"><h3>ROT</h3>
						</div>
						<div className="left pad-right-half">
							<input type="number" id="mRotX" placeholder="X"  disabled = {this.state.locked?true:false}  step=".01" onKeyDown={this.handleKeyDown} value={this.state.mediaInspectorValue.mRotX} onChange={this.getValue} title="X" />

						</div>
						<div className="left pad-right-half"><input id="mRotY" type="number" placeholder="Y"  disabled = {this.state.locked?true:false}  step=".01" onKeyDown={this.handleKeyDown} value={this.state.mediaInspectorValue.mRotY} onChange={this.getValue} title="Y" />

						</div>
						<div className="left"><input id="mRotZ" type="number" placeholder="Z" step=".01"  disabled = {this.state.locked?true:false}  onKeyDown={this.handleKeyDown} value={this.state.mediaInspectorValue.mRotZ} onChange={this.getValue} title="Z" />

						</div>
					</div>
					{/* <!-- Scale --> */}
					<div className="media-row-half">
						<div className="left media-cell-one pad-right-half"><h3>SCALE</h3></div>
						<div className="left pad-right-half"><input id="mRotX" type="number" placeholder="X"  disabled = {this.state.locked?true:false}  onKeyDown={this.handleKeyDown} value={this.state.mediaInspectorValue.mScaleX} onChange={this.getValue} step=".01" title="X" /></div>
						<div className="left pad-right-half"><input id="mScaleY" type="number" placeholder="Y"  disabled = {this.state.locked?true:false}  onKeyDown={this.handleKeyDown} value={this.state.mediaInspectorValue.mScaleY} onChange={this.getValue} step=".01" title="Y" /></div>
						<div className="left"><input id="mScaleZ" type="number" placeholder="Z"  disabled = {this.state.locked?true:false}  step=".01" onKeyDown={this.handleKeyDown} value={this.state.mediaInspectorValue.mScaleZ} onChange={this.getValue} title="Z" /></div>
					</div>
					{/* <!-- Headings --> */}
					<div className="media-row-half">
						<div className="left media-cell-one pad-right-half">&nbsp;</div>
						<div className="left media-cell-one pad-right-half"><h3>X</h3></div>
						<div className="left media-cell-one pad-right-half"><h3>Y</h3></div>
						<div className="left media-cell-one"><h3>Z</h3></div>
					</div>
				</div>
			</div>


		);
	}
}